.modal-form {
  &__container {
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 20px;
  }
  &__title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 21px;
    line-height: 26px;
    color: #3c3c3b;
  }
}
.delete-card {
  max-width: 382px;
  p {
    margin-bottom: 32px;
    width: 100%;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
  }
  .modal__wrapper {
    padding: 65px 0px 0px;
  }

  .buttons__confirm-modal {
    display: flex;
    justify-content: space-between;
    .form-edit-card__button {
      max-width: 181px !important;
      margin-left: 2px;
      border-radius: 8px;
      background: rgba(228, 0, 58, 0.12) !important;
      backdrop-filter: blur(96px);
      transform: matrix(1, 0, 0, 1, 0, 0);
      color: #e4003a !important;
    }

    .form-edit-card__button:hover {
      background: rgba(228, 0, 58, 0.2) !important;
    }
    .form-cancel__button {
      max-width: 181px !important;
    }
  }
}
