.compensation {
  font-family: Montserrat;
  font-style: normal;

  .compensation__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 39px 100px 39px;

    font-weight: bold;
    font-size: 21px;
  }

  .booking-btn {
    display: none;
  }

  .compensation__header--primary {
    background: #F8F7F7;
  }

  .compensation__actual-report {
    padding-left: 100px;
    padding-right: 100px;

    font-weight: 600;
    font-size: 18px;

    &::after {
      content: "";
      height: 1px;
      display: block;
      margin: 48px 0 48px;

      background: #CFD2D3;
      opacity: 0.5;
    }
  }

  .compensation__actual-report-title {
    padding: 32px 0 18px;
  }

  .compensation__actual-report-container {
    display: flex;
    gap: 20px;
    flex-direction: row-reverse;
    justify-content: flex-end;
  }

  .compensation__find-report {
    padding-left: 100px;

    .time__container-text {
      font-family: Roboto;
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
    }
  }

  .compensation__find-report-title {
    margin-bottom: 18px;
    font-weight: 600;
    font-size: 18px;
  }
}

@media (max-width: 992px) {
  .compensation {

    .compensation__header {
      margin-top: 60px;
      padding: 39px 26px 39px;
    }

    .booking-btn {
      display: flex;
      font-weight: normal;
    }

    .compensation__actual-report {
      padding-left: 26px;
      padding-right: 26px;
    }

    .compensation__find-report {
      padding-left: 26px;
      padding-right: 26px;
    }
  }
}




