@media (max-width: 1200px) {
  .reservation {
    flex-direction: column-reverse;
  }
  .reservation__data-picker {
    background: #FFFFFF;
    display: none;

    .reservation__data-picker-group {
      margin: 0;
      padding-top: 8px;
      margin-left: 27px;
      margin-right: 27px;
      max-width: 704px;
      width: 100%;
      border-top: 1px solid rgba(207, 210, 211, 0.5);

      .reservation__data-picker-title {
        display: none;
      }

      .react-datepicker__navigation--previous {
        left: 20px !important;
      }

      .react-datepicker__navigation--next {
        right: 40px !important;
      }
    }
  }

  .reservation__data-picker-group .react-datepicker {
    display: flex;
    justify-content: space-between;
  }

  .reservation__calendar {
    display: none;
  }

  .reservation__data-picker-footer {
    flex-direction: row;
    justify-content: space-around;
  }

  .line {
    display: none;
  }

  .line-tablet {
    display: block;
    border-top: 1px solid rgba(207, 210, 211, 0.5);
    margin-top: 0px;
    margin-left: 17px;
    margin-right: 14px;
    padding-bottom: 30px;
  }

  .reservation__btn {
    margin-top: 130px;
  }

  .time__arrow-right {
    margin-top: 12%;
    margin-left: 6px;
    margin-right: 10px;
  }

  .reservation__data-picker--active {
    display: flex;
  }
}

@media (max-width: 1024px) {
  .reservation__data-picker-group {
    margin-left: 9%;

    .react-datepicker__navigation--previous {
      left: 20px !important;
    }

    .react-datepicker__navigation--next {
      right: 40px !important;
    }
  }
}

@media (max-width: 992px) {
  .reservation__data-picker-group {

    .react-datepicker__navigation--previous {
      left: 20px !important;
    }

    .react-datepicker__navigation--next {
      right: 40px !important;
    }

  }
}

@media (max-width: 768px) {
  .reservation__data-picker-group {

    .react-datepicker__navigation--previous {
      left: 20px !important;
    }

    .react-datepicker__navigation--next {
      right: 45px !important;
    }

  }
}


@media (max-width: 658px) {
  .reservation__data-picker-group {

    .react-datepicker {
      padding: 0;
      padding-top: 24px;
    }

    .react-datepicker__current-month {
      padding-left: 48px;
    }

    .react-datepicker__header {
      border-bottom: none;
    }
  }

  .reservation__data-picker .reservation__data-picker-group {
    align-items: center;
  }

  .reservation__data-picker .reservation__data-picker-group .react-datepicker__navigation--next {
    right: 10px !important;
  }

  .reservation__data-picker .reservation__data-picker-group .react-datepicker__navigation--previous {
    left: 13px !important;
}

  .reservation__data-picker-footer {
    flex-direction: column;
    align-items: center;
  }

  .ctrl-button.reservation__btn {
    margin-top: 30px;
    margin-bottom: 32px;
    padding: 17px;
  }

  .time:last-child {
    margin-top: 20px;
  }

  .line-tablet {
    margin-left: 0px;
    margin-right: 0px;
    padding-bottom: 20px;
  }

  .ctrl-button.reservation__btn {
    width: 288px;
  }

  .reservation__data-picker-group .react-datepicker__day-names {
    width: 250px !important;
    margin-left: 13px !important;
  }

  div.react-datepicker__month {
    width: 253px;
    margin-left: 10px;
  }

  .reservation__data-picker-group .react-datepicker__week {
    width: 100%;
  }
}
