.reservation {
  display: flex;

  &__data-picker {
    display: flex;
    justify-content: center;
    background-color: rgba(0, 161, 223, 0.02);

    &-group {
      display: flex;
      flex-direction: column;
      padding-top: 58px;
      margin-left: 27px;
      margin-right: 27px;

      .react-datepicker {
        border: none;
        background-color: transparent;
        width: auto !important;
      }

      .react-datepicker__day--in-selecting-range {
        background-color: rgba(0, 161, 223, 0.06) !important;
      }

      .react-datepicker__day--selected {
        background-color: #00a1df !important;
      }

      .react-datepicker__day--in-range:hover {
        background-color: rgba(0, 161, 223, 0.06);
      }

      .react-datepicker__day--range-end {
        background-color: #00a1df !important;;
      }
    }

    &-title {
      font-family: Montserrat;
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 15px;
      color: #3C3C3B;
    }

    &-footer {
      display: flex;
      flex-direction: column;
    }
  }

  &__calendar {
    display: flex;
    flex: 1 1 0;
  }

  &__date-icon {
    --icon-path-primary: #3C3C3B; 
    margin-right: 16px;
    flex-shrink: 0;  
  }

  &__date-icon.icon--fill {
    margin-right: 6px;
  }

  &__btn {
    margin-bottom: 88px;
  }

  &__data-picker-group {

    .react-datepicker__day {
      width: 36px;
      height: 36px;
      border: none;
      box-sizing: border-box;
      border-radius: 8px;
      padding: 0;
      margin: 0;
      font-size: 14px;
      line-height: 36px;
      font-weight: 400;
      text-decoration: none;
    }

    .react-datepicker__day--range-start {
      border-radius: 8px !important;

      &:hover {
        border-radius: 8px !important;
      }
    }

    .react-datepicker__day--range-end {
      border-radius: 8px !important;

      &:hover {
        border-radius: 8px !important;
      }
    }

    .react-datepicker__day--in-range{
      border-radius: 0;
    }

    .react-datepicker__day--in-selecting-range {

      &:not(.react-datepicker__day--selecting-range-start, .react-datepicker__day--selecting-range-end) {
        border-radius: 0 ;
      }
    }

    .react-datepicker__week {
      display: flex;
      width: 90%;
    }

    .react-datepicker-popper {
      margin-left: -15px !important;
      padding-top: 0 !important;
    }

    .react-datepicker__day-names {
      margin-bottom: -8px;
      display: flex;
      justify-content: space-between;

      width: 250px !important;
      margin-left: 12px !important;
    }

    .react-datepicker__year-dropdown {
      width: 70px;
      border: 0.5px solid #CFD2D3;
      box-sizing: border-box;

      box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);
      border-radius: 8px;
    }

    .react-datepicker__year-option {
      padding: 5px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  &_data-picker--active {
    display: flex !important;
  }
}

.time {
  display: flex;
  &__title{
  margin-bottom: 12px;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
}
  &__duration {
    .react-datepicker__header {
      border-bottom: none;
      font-family: Roboto;
      font-style: normal;
      font-weight: 500;
      font-size: 15px;
      line-height: 18px;
      display: flex;
      align-items: center;
      text-align: center;
      color: #3C3C3B;
    }

    .react-datepicker__time-container {
      background: #FFFFFF;
      border: 0.5px solid #CFD2D3;
      box-sizing: border-box;
      box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);
      border-radius: 8px;
      width: auto;
    }

    .react-datepicker__header--time {
      width: 100px;
      padding-left: 0;
      padding-right: 0;
      padding-top: 20px;
      justify-content: center;
    }

    .react-datepicker__time-list {
      scrollbar-width: thin;
    }

    .react-datepicker__time-list::-webkit-scrollbar { 
      width: 3px;               
    }
    
    .react-datepicker__time-list::-webkit-scrollbar-thumb {
      background-color: #3c3c3b;
      border-radius: 20px;
    }

    .react-datepicker__time-container .react-datepicker__time .react-datepicker__time-box ul.react-datepicker__time-list li.react-datepicker__time-list-item--selected {
      background-color: #00A1DF;
      border-radius: 8px;
      display: flex;
      justify-content: center;
      align-items: center;

      &:hover {
        border: 2.2px solid rgba(0, 161, 223, 0.18);;
      }
    }

    .react-datepicker__time-container .react-datepicker__time .react-datepicker__time-box {
      width: auto;
    }

    .react-datepicker__time-container .react-datepicker__time .react-datepicker__time-box ul.react-datepicker__time-list li.react-datepicker__time-list-item {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  &__arrow-right {
    margin-top: 12%;
    margin-left: auto;
    margin-right: auto;
    flex-shrink: 0;
  }

  &__container {
    background: rgba(0, 161, 223, 0.02);
    border-radius: 8px;
    display: flex;
    align-items: center;
    height: 48px;
    width: 124px;
    justify-content: space-evenly;

    background: #FFFFFF;

    outline: 0.5px solid #CFD2D3;

    cursor: pointer;

    transition-property: all;
    transition-duration: 0.25s;
    transition-timing-function: linear;

    p {
      font-size: 16px;
      line-height: 19px;
      color: #CFD2D3;
      font-style: normal;
      font-weight: normal;
      width: 110px;
      padding-left: 3px;
      text-align: center;
    }

    &:hover {
      outline: 2.2px solid rgba(0, 161, 223, 0.18);
    }

    &:active {
      outline: 2.2px solid rgba(0, 161, 223, 0.18);

    }

    &--active {
      border: 0.5px solid #00A1DF !important;
      background-color: rgba(0, 161, 223, 0.06) !important;
      color: #00A1DF !important;

    }
  }

  &--active {
    color: #00A1DF !important;
  }
}

.time__duration .time__title, .dropdown-search__title {
  margin-bottom: 12px;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
}

.calendar__time {
  display: flex;
  height: 48px;
  font-style: normal;
  font-weight: 400 !important;
  font-size: 16px;
  line-height: 19px;
  align-items: center;
  color: #CFD2D3;
  background: #FFFFFF;
  box-sizing: border-box;
  border-radius: 8px;

  .icon--fill {
    flex-shrink: 0;
    width: 10px;
    --icon-path-primary: #3C3C3B;
    margin-right: 14px;
  }

  &-group {
    border: 0.5px solid #CFD2D3;
    border-radius: 8px;
    cursor: pointer;
    transition-property: all;
    transition-duration: 0.25s;
    transition-timing-function: linear;
    display: flex;
    height: 48px;
    align-items: center;
    width: 124px;

    &:hover {
      box-shadow: 0 0 0 2px rgb(0 161 223 / 18%);
    }

    &:active, &:focus {
      outline: none;
      box-shadow: 0 0 0 2px rgb(0 161 223 / 18%);
      border-color: #00a1df;
      background-color: #fff;
    }

  }

}

.calendar__text {
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol' !important;
  font-style: normal !important;;
  font-weight: normal !important;
  font-size: 16px !important;;
  line-height: 19px !important;;
}

.line {
  border-top: 1px solid rgba(207, 210, 211, 0.5);
  margin-top: 32px;
  margin-bottom: 32px;
}

.line-tablet {
  display: none;
}

.none {
  display: none !important;
}

.res-active {
  display: flex !important;
}

.time__container--empty {
  border: 0.5px solid #E4003A;
  box-shadow: 0 0 0 2px rgb(228, 0, 58, 0.18);
  color: #E4003A !important;

  transition-property: border-color, color, background-color;
  transition-duration: 0.25s;
  transition-timing-function: ease;
}

.time__container--empty:hover {
  border: 0.5px solid #E4003A;
  box-shadow: 0 0 0 2px rgb(228, 0, 58, 0.18);
}

.time__container--empty .reservation__date-icon {
  --icon-path-primary: #E4003A;
}

.time__container--empty .icon--fill {
  --icon-path-primary: #E4003A;
}

.react-datepicker__day--disabled {
  color: #cfd2d3 !important;
  cursor: pointer !important;
}

.my-reservation .react-datepicker-popper {
  left: 0% !important;
  top: 103% !important;
  margin-left: 0px !important;
  padding-top: 0px !important;
  transform: initial !important;
  inset: initial !important;
}