.search-filter {
    display: none;
    min-width: 172px;
    background-color: #fff;
    border-width: 1px;
    border-style: solid;
    border-color: rgba(207, 210, 211, 0.5);
    border-radius: 8px;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);
    z-index: 99;

    &--active {
        display: flex;
        flex-direction: column;
    }

    &__field {
        background: url("../../../assets/images/search/search.svg") no-repeat 13px calc(50% - 1px);
        width: 100%;
        border: none;
        padding: 14.5px 28px 14.5px 48px;

        &::placeholder {
            color: #CFD2D3;
        }
    }

    &__location {
        padding: 12px 0px 12px 12px;
        border-top-width: 1px;
        border-top-style: solid;
        border-top-color: rgba(207, 210, 211, 0.5);
    }

    &__content {
        height: 172px;
        overflow-y: auto;
        scrollbar-width: none;

        &-btn {
            width: 100%;
            background-color: transparent;
            border-radius: 8px;
            padding: 16px 20px;
            text-align: left;

            &:hover {
                background-color: #00A1DF;
                color: #fff;

                .search-filter__room {
                    color: #fff;
                }
            }
        }

        &::-webkit-scrollbar {
            width: 3px;
        }
        
        &::-webkit-scrollbar-thumb {
            background-color: #3c3c3b;
            border-radius: 20px;
        }
    }

    &__room {
        font-size: 12px;
        color: #CFD2D3;
        margin-right: 4px;
    }
}