.tooltip_container {
  padding: 10px 0px;
  top: 254px;
  left: -50px;
  z-index: 333;
  max-width: max-content;
  min-width: 190px !important;
  max-height: max-content;
  position: absolute;
  background: #ffffff;
  border: 0.5px solid #cfd2d3;
  box-sizing: border-box;
  box-shadow: 0px 10px 18px rgba(0, 0, 0, 0.08);
  border-radius: 8px;
  display: flex;
  padding: 10px;
  &:last-child {
    margin-right: 0px;
  }
  .hr {
    border: 0.5px solid #cfd2d3;
    opacity: 0.48;
    border-radius: 8px;
    margin-top: 12px;
    margin-bottom: 17px;
  }
  .tooltip_content{
    color: #3C3C3B;
    width: 190px;
    margin-right: 12px;
    &:last-child {
      margin-right: 0px;
    }
  }
}

