.history {
  &__wrapper {
    font-family: 'Montserrat';
    font-style: normal;
    color: #3c3c3b;
  }
  &__header {
    background: #f8f7f7;
    padding: 39px 100px;
    font-weight: 700;
    font-size: 21px;
    line-height: 26px;
  }
  &-list {
    padding: 0px 100px;
    margin-bottom: 70px;
    &__item {
      margin-top: 32px;
    }
  }
  .version {
    &__date {
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 24px;
    }
    &__wrapper {
      font-family: 'Roboto';
      font-style: normal;
      padding: 20px;
      border: 1px solid #ececec;
      box-sizing: border-box;
      border-radius: 8px;
    }
    &__category {
      font-weight: 400;
      font-size: 20px;
      line-height: 23px;
      color: #00a1df;
      margin-bottom: 24px;
    }
    &__info {
      margin-bottom: 24px;
      font-weight: 400;
      font-size: 17px;
      line-height: 140%;
      p {
        font-weight: 400;
        font-size: 17px;
        line-height: 140%;
      }
      &:last-child {
        margin-bottom: 0px;
      }
    }
  }
  .menu {
    padding-left: 30px;
    &__item {
      list-style-type: decimal;
    }
  }
}
@media (max-width: 991px) {
  .history {
    margin-top: 60px;
  }
}
@media (max-width: 768px) {
  .history {
    &-list {
      padding: 0px 20px;
    }
    &__header {
      padding: 39px 20px;
    }
  }
}
