.back {
    display: none;
    padding: 14px 20px;
    background-color: #F8F7F7;
    border-bottom: 1px solid rgba(207, 209, 212, 0.5);
}

.back__btn {
    display: flex;
    align-items: center;
    grid-column-gap: 30px;
    background-color: transparent;
    padding: 0px 3px;
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 17px;
    line-height: 123%;
    color: #3C3C3B;
}