.wrapper_card {
  border-radius: 8px;
  border: 0.5px solid #00a783;
  padding: 4px;
  background-color: #fff;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  position: relative;
  text-decoration: none;
  color: #3c3c3b;

  &:active,
  &:hover {
    text-decoration: none;
    color: #3c3c3b;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.08);
  }
}

.workspace__card--full-time {
  border-color: #e4003a;
}

.workspace__card--full-time .card__content .card-tool__wrapper {
  border-top-color: #e4003a;
}

.workspace__card--full-time .user-bar__avatar {
  border-color: #e4003a;
  background-color: rgba(228, 0, 58, 0.12);
  color: #e4003a;
}

.workspace__card_hover .card__checkbox {
  display: block;
}
.workspace__card_hover {
  .card {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 278px;
    height: 319px;
    border-radius: 8px;
    border: 0.5px solid #cfd2d3;
    padding: 4px;
    background-color: #fff;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    box-shadow: 0 0 15px rgba(95, 95, 95, 0.2);
  }
  .tool_container {
    display: block;
    background-color: white;
    padding-left: 16px;
    padding-right: 14px;
  }
}

.card-tool__wrapper {
  padding-left: 10px;
  padding-right: 8px;
}

.card-tool__wrapper-item {
  width: 34px !important;
  height: 24px !important;
}
.k {
  width: 40px !important;
  height: 34px !important;
}
.keyboard {
  bottom: 0 !important;
}
.no-border {
  border: none;
}

.card-tool__wrapper-check {
  top: 0;
}

@media (max-width: 768px) {
  .wrapper_card {
    width: 100%;
    margin-left: 0px !important;
    &:hover {
      width: 100%;
    }
  }
  .workspace__card_hover .card {
    width: 100%;
  }
  .ctrl-button__container {
    width: 100%;
  }
}

.card-owner {
  display: flex;
  flex-direction: row;
}
.card__content {
  .card-tool__wrapper {
    border-top: 1px solid #00a783;
  }
}

.workspace__card--inactive .card__content .card-tool__wrapper {
  border-top-color: #fab526;
}

.workspace__card--inactive .user-bar__avatar {
  border-color: #fab526;
  background-color: #fff5df;
  color: #fab526;
}

.workspace__card--part-inactive {
  border-color: #fab526;
}
.workspace__card--part-inactive .card__content .card-tool__wrapper {
  border-top-color: #fab526;
}

.workspace__card--part-time {
  border-color: #00a1df;
}

.workspace__card--part-time .card__content .card-tool__wrapper {
  border-top-color: #00a1df;
}
