.location-filter {
    display: none;
    background-color: #fff;
    min-width: 232px;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);
    padding: 12px 0px 12px 12px;
    z-index: 122;
}

.location-filter--active {
    display: flex;
    flex-direction: column;
}

.location-filter__wrapper {
    height: 196px;
    overflow-y: auto;
    scrollbar-width: none;
}

.location-filter__wrapper::-webkit-scrollbar { 
    width: 3px;               
}

.location-filter__wrapper::-webkit-scrollbar-thumb {
    background-color: #3c3c3b;
    border-radius: 20px;
}