@media(max-width: 1560px) {
    /* Schedule-Dashboard */
    .schedule-dashboard__box {
        width: 100%;
        margin-top: 20px;
    }
    .schedule-dashboard__place {
        margin-right: 0px;
    }
}

@media (max-width: 1400px) {
    /* Page Login */
    .authtorisation::after {
        width: 360px;
    }
    .header__form-search {
        margin-right: 30px;
    }
}

@media(max-width: 1370px) {
    /* Schedule-Dashboard */
    .schedule-dashboard__select-time {
        width: 100%;
        margin-top: 20px;
    }
    .schedule-dashboard {
        padding-left: 15px;
        padding-right: 15px;
    }
}

@media (max-width: 1200px) {
    /* Header */
    .user-bar__booking {
        margin-right: 24px;
    }
    /* Schedule-Dashboard */
    .schedule-dashboard__plus {
        display: block;
    }
    .schedule__reservation--desktop .schedule-dashboard {
        max-width: 734px;
        margin: 0px auto 0 auto;
        border-bottom: none;
        padding-top: 42px;
        padding-bottom: 42px;
    }
    .schedule__reservation--desktop .schedule-dashboard__select-time,
    .schedule__reservation--desktop .calendar,
    .schedule__reservation--desktop .schedule-dashboard__plus
    {
        display: none;
    }
    .schedule__reservation--desktop .schedule-dashboard__box {
        width: auto;
        margin-top: 0px;
        justify-content: end;
    }
    /* Schedule-Weeks */
    .schedule-weeks--none {
        display: none;
    }
}

@media (max-width: 992px) {
    /* Page Login */
    .authtorisation::after {
        width: 260px;
    }
    .reservation__data-picker-group {
        margin-top: 50px;
    }
    /* SideBar */
    .sidebar {
        position: fixed;
        left: -100%;
        top: 61px;
        bottom: 0;
        width: 343px;
    }
    .sidebar--is-open {
        left: 0;
    }
    .sidebar__content {
        overflow-y: auto;
    }
    /* Header */
    .header {
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 6;
    }
    .form-search {
        max-width: 508px;
    }
    .header__form-search {
        margin-right: 0px;
    }
    .user-bar__info, .header__form-search {
        display: none;
    }
    .booking-btn--desktop {
        font-size: 0px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 36px;
        height: 36px;
        padding: 0px;
        border-style: none;
        border-radius: 50%;
    }
    .booking-btn__icon {
        display: block;
    }
    .form-search--is-active, .form-search__btn--is-active, .header__tools, .header__logo {
        display: block;
    }
    .form-search__input {
        padding-top: 8px;
        padding-bottom: 7px;
    }
    .header__tools--none, .header__logo--none, .user-bar--none, .main--none {
        display: none;
    }
    .header__container--center {
        justify-content: center;
    }
    /* Main */
    .main {
        padding-top: 61px;
    }
    /* Schedule */
    .schedule__dates {
        display: none;
    }
    /* Schedule-Dashboard */
    .schedule-dashboard__select-time {
        width: auto;
        margin-top: 0px;
    }
    .schedule-dashboard {
        padding: 71px 20px 20px;
    }
    .schedule-dashboard__box {
        margin-top: 44px;
    }
    .schedule-dashboard__date {
        order: 4;
    }
    .schedule__reservation--desktop .schedule-dashboard__back-btn {
        display: none;
    }
    .schedule__reservation--desktop .schedule-dashboard {
        margin-top: 43px;
    }
}

@media (max-width: 768px) {
    /* Page Login */
    .authtorisation::after {
        display: none;
    }
    .authtorisation__group {
        max-width: 366px;
    }
    .authtorisation__description, .authtorisation__logo-block {
        text-align: center;
    }
    .form-authtorisation__login {
        margin-bottom: 21px;
    }
    /* Schedule-Dashboard */
    .schedule-dashboard__date, .schedule-dashboard__select-time {
        width: 100%;
    }
    .schedule-dashboard__date, .schedule-dashboard__box {
        margin-top: 20px;
    }
    .schedule-dashboard__date {
        justify-content: space-between;
    }
    .schedule-dashboard__select-time-btn {
        width: 100%;
    }
    .schedule-dashboard {
        padding-top: 73px;
    }
    .schedule__reservation--desktop .schedule-dashboard__date {
        justify-content: end;
        margin-top: 0px;
    }
    /* Schedule-Weeks */
    .time-line__user {
        width: 24px;
        height: 24px;
        margin-top: 4px;
        margin-bottom: 4px;
    }
    .time-line__checkbox-wrapper {
        padding-top: 9px;
        padding-bottom: 9px;
    }
    .time-line__checkbox {
        width: 15px;
        height: 15px;
    }
    .time-line__checkbox:not(:last-child) {
        margin-bottom: 25px;
    }
}

@media (max-width: 576px) {
    /* Page Login */
    .authtorisation__logo-block {
        margin-bottom: 32px;
    }
    .authtorisation__title {
        font-size: 17px;
    }
    .authtorisation__text {
        font-size: 12px;
    }
    /* SideBar */
    .sidebar {
        width: 100%;
    }
    /* Header */
    .header__logo {
        display: none;
    }
    /* Schedule-Dashboard */
    .schedule__reservation--desktop .schedule-dashboard {
        padding-top: 22px;
        padding-bottom: 22px;
    }
    /* Schedule-Weeks */
    .schedule-weeks__place {
        max-width: 50px;
    }
    .schedule-weeks__header-day-month {
        display: none;
    }
    .schedule-weeks__header-day, .schedule-weeks__booking-column {
        padding-left: 3px;
        padding-right: 3px;
    }
}
