.sidebar {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    width: 248px;
    z-index: 100;
    background-color: #fff;
    border-right: 1px solid rgba(207, 210, 211, 0.5);

    &__header {
        padding: 32px 24px;
    }

    &__title {
        display: flex;
        align-items: center;
        font-size: 17px;
        line-height: 120%;
        padding: 20px 20px 20px 34px;

        &-icon {
            margin-right: 14px;
            color: #cfd2d3;
        }
    }

    &__copyrights {
        margin-top: auto;
    }
}

.nav__menu-link {
    display: block;
    padding: 20px 20px 20px 68px;
    position: relative;
    font-size: 15px;
    line-height: 120%;
    color: rgba(61, 61, 59, 0.8);
    transition-property: background-color, color;
    transition-duration: 0.25s;
    transition-timing-function: ease;
}

.nav__menu-link--active:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 4px;
    border-radius: 4px 0 0 4px;
    background-color: #00a1df;
}

@media (any-hover: hover) {
    .nav__menu-link:hover {
        background-color: rgba(0, 161, 222, 0.06);
        color: #00a1df;
    }
}

.nav__menu-link--active {
    color: #00a1df;
    background-color: rgba(0, 161, 223, 0.06);
}