.user-bar {
  height: 36px;
  font-weight: 500;
  border-radius: 50%;
  box-shadow: inset 0 0 0 2px #fff;
  text-align: center;
  line-height: 30px;

  &__avatar {
    &--inactive {
      border: 1px solid #fab526;
      background-color: #fdf0d4;
      color: #fab526;
    }

    &--pers {
      background: rgba(0, 161, 223, 0.1);
      border: 1px solid rgba(0, 161, 223, 0.32);
      color: #00a1df;
    }

    &--foreign {
      background: #fdd1dc;
      border: 1px solid rgba(228, 0, 58, 0.32);
      -webkit-box-shadow: inset 0 0 0 2px #fff;
      box-shadow: inset 0 0 0 2px #fff;
      color: #e4003a;
    }
  }
  &__group {
    display: flex;
    align-items: center;

    .user-bar__avatar {
      line-height: 36px;
    }
  }
}
