.dropdown-search {
  --arrow-down: black;

  &__container {
    position: relative;
  }

  .dropdown-search__input-block {
    width: 300px;
    position: relative;
  }

  .dropdown-search__icon {
    width: 32px;
    position: absolute;
    right: 14px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;

    --icon-background-primary: transparent;
    --icon-path-primary: #CFD2D3;

  }

  .calendar__time-group--searching {
    .dropdown-search__icon {
      color: #00A1DF;
    }
  }

  .calendar__time-group--searched {
    color: #3C3C3B !important;
    border-color: #CFD2D3 !important;
    background: #FFFFFF !important;
  }

  .form-search__remove-icon--idle {
    color: #CFD2D3 !important;
    --icon-path-primary: #FFFFFF !important;
  }

  .dropdown-search__icon--focus {
    --icon-background-primary: transparent;
    --icon-path-primary: #00A1DF;
  }

  .dropdown-search__icon--searching {
    color: transparent;
    --icon-path-primary: #00A1DF;
  }

  .calendar__time-group {
    width: 100%;
    padding: 14px;
    outline: none;
    color: #3C3C3B;

    box-shadow: none;


    &::placeholder {
      font-family: Roboto;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      color: #CFD2D3 !important;
    }

    @media (max-width: 420px) {
      width: 288px;
    }

  }

  .calendar__time-group--searching {
    border-color: #00a1df;
    background: rgb(234, 247, 251);

    .dropdown-search__icon {
      --icon-outer-circle: red;
      --icon-outer: rgb(0, 161, 223);
      --icon-inner: rgb(0, 161, 223);
    }

  }


  .search-modal {
    margin-top: 4px;
    position: absolute;
    right: 0;
    max-width: 232px;
    width: 100%;
  }

  @media (max-width: 1200px) {
    margin-left: 4%;
  }

  @media (max-width: 420px) {
    margin-left: 0;
    width: 288px;
  }

}


