.schedule-month {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: repeat(6, minmax(142px, auto));
  overflow-x: auto;

  &__day-week,
  &__data,
  &__current-month {
    font-weight: 500;
  }

  &__day {
    padding: 12px 5%;
    text-transform: capitalize;
    border-bottom: 1px solid rgba(207, 210, 211, 0.5);

    &-week {
      color: #cfd2d3;
      margin-bottom: 4px;
    }

    &:not(:nth-child(7n)) {
      border-right: 1px solid rgba(207, 210, 211, 0.5);
    }
  }

  &__data {
    margin-right: 4px;
  }

  &__current-month {
    display: inline-block;
  }

  &__booked {
    display: flex;
    justify-content: center;
    max-width: 96px;
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;

    .user-bar__avatar {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  &__all-booked {
    font-weight: 400;
    text-align: center;
    line-height: 2;
    background-color: #cfd2d3;
    color: #fff;
  }

  .schedule-month__ava {
    border: 3px solid rgba(0, 161, 223, 0.4);
    cursor: pointer;
  }

  .schedule-month__users .schedule-month__ava,
  .user__ava__full-reserved .schedule-month__ava {
    border: none;
  }

  .user__ava__full-reserved {
    border-color: rgba(228, 0, 58, 0.4) !important;
  }
}

.schedule-month__ava,
.schedule-month__all-booked,
.user__ava,
.user-bar__avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;

  &:not(:first-child) {
    margin-left: -12px;
  }
}

.schedule-month__ava,
.user__ava {
  object-fit: cover;
}

.users {
  min-width: 320px;
  max-height: 341px;
  background-color: #fff;
  border-radius: 8px;
  border: 1px solid rgba(207, 210, 211, 0.5);
  box-shadow: 0px 10px 18px rgba(0, 0, 0, 0.08);
  padding: 11px;
  overflow-y: auto;
  z-index: 1;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    width: 5px;
  }

  &::-webkit-scrollbar-thumb {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  }
  .user-bar__avatar {
    width: 32px;
    height: 32px;
    font-size: 16px;
    line-height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.schedule-week__users {
  max-width: 200px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;

  .user-bar__avatar {
    width: 32px;
    height: 32px;
    font-size: 16px;
    line-height: 32px;
    &--inactive {
      background: #e6f6fc;
      border: 1px solid #fab526;
      color: #fab526;
      background: #f6ebd2;
      &:hover {
        border-color: #fab526;
      }
    }
  }
}

.user {
  display: flex;
  font-size: 14px;

  &:not(:last-child) {
    padding-bottom: 12px;
    border-bottom: 1px solid rgba(207, 210, 211, 0.5);
  }

  &:not(:first-child) {
    padding-top: 12px;
  }

  &__name {
    margin-left: 8px;
    white-space: nowrap;
    max-width: 140px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    color: #3c3c3b;
    margin-top: 9px;
    @media (max-width: 420px) {
      white-space: normal;
    }
  }

  &__booking {
    margin-left: auto;
    background-color: rgba(0, 161, 223, 0.12);
    border-radius: 8px;
    line-height: 17px;
    color: #00a1df;
    background-color: white !important;
    &__item {
      margin-left: auto;
      background-color: rgba(0, 161, 223, 0.12);
      border-radius: 8px;
      line-height: 17px;
      color: #00a1df;
      padding: 4px 8px 3px 8px;
      margin-top: 6px;
      &:first-child {
        margin-top: 2px;
      }
    }
  }
  .item--border {
    border: 1px solid #00a1df;
    &--inactive {
      border: 1px solid #fab526;
    }
  }
}
