.report-search {
  display: flex;

  .report-search__date {
    width: 361px;
    height: 114px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-right: 12px;
    padding: 20px;

    background: #F8F7F7;
    border: 0.3px solid #CFD2D3;
    border-radius: 4px;
  }

  .report-search__date-title {
    margin-bottom: 12px;

    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
  }

  .report-search__button {
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;

    background: #F8F7F7;
    border-radius: 8px;
    cursor: pointer;

    --download-upper: #3C3C3B;
    --download-lower: #3C3C3B;

  }

  .report-search__button--active {
    background: rgba(0, 161, 223, 0.12);

    .report__icon {
      border-radius: 8px;

      --download-upper: #00A1DF;
      --download-lower: #00A1DF;
    }
  }

  .report-search__date {
    &:last-child {
      width: 165px;
    }
  }

  .report-search__date-container {
    --arrow-down: black;

    .calendar__time-group {
      padding: 14px;
      justify-content: space-between;

      &:hover {
        border: 0.5px solid #CFD2D3 !important;
      }
    }

    .calendar__time {
      &:hover {
        border-color: transparent;
      }
    }

    .time__container--active {
      border: 0.5px solid #CFD2D3 !important;
      background-color: transparent !important;
      color: black !important;

    }

    .calendar__text {
      width: auto;

      &:first-letter {
        text-transform: uppercase;
      }
    }

    .react-datepicker-year-header {
      display: none;
    }

    .react-datepicker__navigation--next {
      display: none;
    }

    .react-datepicker__navigation--previous {
      display: none;
    }

    .react-datepicker__year {
      max-height: 195px;
      overflow: auto;
      margin: 0;
      padding: 12px;

      &::-webkit-scrollbar {
        width: 0;
      }
    }

    .react-datepicker__year-wrapper {
      flex-direction: column;
    }

    .react-datepicker__year-text {
      padding: 16px 61px 16px 55px;
      width: auto;
    }

    .react-datepicker {
      width: auto !important;
      padding: 0;
    }

    .react-datepicker__month {
      width: auto;
      margin: 0;
      padding: 30px 37px;

    }

    .react-datepicker__month-wrapper {
      width: 253px;
      display: flex;
      justify-content: space-between;

    }

    .react-datepicker__month-container {
      float: none;
      width: auto;
    }

    .react-datepicker__month-text--keyboard-selected {
      width: 53px;
      height: 30px;
      border-radius: 8px;
    }

    .react-datepicker-popper {
      margin-left: 0;
    }

    .react-datepicker__month-text {
      width: 53px;
      height: 30px;
      font-family: Roboto;
      font-weight: 500 !important;

      &:first-letter {
        text-transform: uppercase;
      }
    }


    &:last-child {

      .calendar__time-group {
        width: 165px;
      }
    }

  }
}

@media (max-width: 992px) {
  .report-search {
    .report-search__date {
      width: 100%;
      justify-content: start;
    }

    .report-search__date-container {
      &:last-child {
        margin-left: 32px;

        .calendar__time-group {
          width: 250px;
        }
      }
    }
  }
}
