@media (max-width: 488px) {
  .user-filter_title {
    margin-top: 32px;
  }
  .filters {
    &__button {
      &--wrapper {
        margin-top: 12px;
      }
    }
    &_header {
      margin: 26px 18px;
      padding: 0px;
      display: block;
      width: auto;
    }
  }
}
@media (max-width: 432px) {
  .filters {
    &_wrapper {
      .filters__button {
        font-family: Roboto;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 19px;
        align-items: center;
        text-align: center;
        background: #00a1df !important;
        color: #f8f7f7 !important;
        padding-left: 81px;
        padding-right: 81px;
      }
      &--mobile {
        margin-top: 60px !important;
        .filters__button {
          color: #3c3c3b !important;
          padding: 10px 25px;
          background: #f8f7f7 !important;
        }
      }
      .filter_content {
        margin: 0px 0px;
      }
    }
    &_header {
      &--mobile {
        display: none;
      }
    }
  }
}
.user-filter_previous {
  margin-top: 51px;
  margin-right: 24px;
}
