@media(max-width: 1400px) {
    .schedule-month__ava:nth-child(n+2):not(:last-child) {
        display: none;
    }

    .schedule-month__booked {
        .user-bar__avatar:nth-child(n+2):not(:last-child) {
            display: none;
        }
    }
}

@media(max-width: 768px) {
    .schedule-month {
        grid-template-columns: repeat(7, minmax(107px, 1fr) );
        grid-template-rows: repeat(6, minmax(128px, auto));
    }
}