.delete-schedule {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;

  .delete-schedule__button {
    width: 161px !important;
    height: 40px;
    padding: 0 !important;
    font-size: 16px;
    line-height: 19px;
    border-radius: 8px;

    &:last-child {
      margin-left: 12px;
    }

  }

  .delete-schedule__button--primary {
    width: 212px !important;
    background: none;
    border: 0.5px solid #CFD2D3;
  }

  .delete-schedule__button--danger {
    background: rgba(228, 0, 58, 0.12);
    color: #E4003A;

    &:hover {
      background: rgba(228, 0, 58, 0.2);
    }
  }

  .delete-schedule__count {
    color: rgba(60, 60, 59, 0.68);
    margin-left: 8px;
  }

  .ctrl-button__text {
    margin-right: 0;
  }

  .delete-schedule__title {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: -0.1px;

    display: flex;
    align-items: center;

    color: #3C3C3B;
  }

  .delete-schedule__time-duration {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 15px;
    display: flex;
    align-items: center;

    /* B/Shade/Black */

    color: rgba(60, 60, 59, 0.64);
  }

}

.delete-schedule + .modal-form__background {
  .ctrl-button {
    background: rgba(228, 0, 58, 0.12);
    color: #E4003A;

    &:hover {
      background: rgba(228, 0, 58, 0.2);
    }
  }

  .modal-form__icon-wrapper{
    width: 24px;
    height: 24px;
    right: 20px;
    position: absolute;
    z-index: 1;
  }

  .modal-form__icon-closed {
    top: 0;
    right: 0;
  }
}

@media (min-width: 1560px) {
  .delete-schedule {
    width: calc(100% - 131px);
  }
}

@media (max-width: 1200px) {
  .delete-schedule {
    margin-top: 20px;
  }
}

@media (max-width: 768px) {
  .delete-schedule {
    flex-direction: column;
    align-items: flex-start;
    min-width: 0;
  }
}


@media (max-width: 426px) {
  .delete-schedule {
    align-items: center;

    .delete-schedule__button--primary {
      width: 123px !important;
    }
    .delete-schedule__button--danger {
      width: 142px !important;
    }
  }
}




