.image-box {
  width: 32px;
  height: 32px;
}

.card__owner-avatar {
  width: 32px !important;
  height: 32px !important;

  &:first-child {
    z-index: 2;
  }

  &:nth-child(2) {
    z-index: 1;
  }

  &:nth-child(n + 3):not(:last-child) {
    display: none;
  }

  &:not(:first-child),
  .schedule-month__all-booked:not(:first-child) {
    margin-left: -13px;
  }

  &:last-child {
    border: 2px solid rgb(255, 255, 255);
    float: right;
  }

  .user-bar__avatar {
    width: 32px;
    height: 32px;
    line-height: 32px;
  }
}
.time-line__avatar-text {
  display: flex;
  justify-content: center;
  height: 30px;
  align-items: center;
}

.user .time-line__avatar-text {
  border: 1px solid black;
  border-radius: 50%;
  width: 32px;
  height: 32px;
}

.card-owner__wrapper {
  display: flex;
  flex-direction: row;
  max-width: 86px;
  position: absolute;
  margin-top: 14px;
}

.schedule-day {
  background-color: rgba(207, 210, 211, 1);
  width: 32px !important;
  height: 32px;
  border-radius: 100%;
  text-align: center;
  font-size: 14px;
  line-height: 32px;
  color: #ffffff;
}

.info-reservation {
  &__wrapper {
    max-width: max-content;
    max-height: max-content;
    background: #ffffff;
    border: 0.5px solid #cfd2d3;
    box-sizing: border-box;
    box-shadow: 0px 10px 18px rgba(0, 0, 0, 0.08);
    border-radius: 8px;
    padding: 12px 16px;
  }
  &-item {
    display: flex;
    flex-direction: row;
  }
}

.info-user {
  display: flex;
  flex-direction: row;
  &-item {
    margin-left: 5px;
  }
}

.card-owner__list {
  display: none;
  min-width: 320px;
  max-height: 341px;
  background-color: #fff;
  border-radius: 8px;
  border: 1px solid rgba(207, 210, 211, 0.5);
  box-shadow: 0px 10px 18px rgba(0, 0, 0, 0.08);
  padding: 11px;
  overflow-y: auto;
  z-index: 9999;
  margin-top: 32px;
  margin-left: -80px;
  scrollbar-width: none;

  .user-bar__avatar {
    width: 32px;
    height: 32px;
    line-height: 32px;
  }

  &--active {
    display: block;
  }

  &:empty {
    display: none;
  }

  &::-webkit-scrollbar {
    width: 0px;
  }
}

.card-owner {
  display: flex;
  justify-content: start;
  height: 48px;
  .schedule-month__booked {
    justify-content: left;
    height: 48px;
    margin-left: 0px;
    margin-right: 0px;
  }
}

.card__status--busy {
  max-width: max-content;
  background: rgba(0, 161, 223, 0.12);
  border-radius: 8px;
  padding: 4px 8px;
  color: #00a1df;
  flex-grow: 0;
  font-size: 14px;
  line-height: 16px;
}

.card__status--full-busy {
  color: #e4003a;
  background: rgba(228, 0, 58, 0.12);
}

.date-reservation {
  background: #ffffff;
  border: 1px solid #cfd2d3;
  box-sizing: border-box;
  border-radius: 8px;
  padding: 4px 8px;
}
.count-people {
  display: block !important;
  position: relative;
}

.tool {
  position: absolute;
  z-index: 333333333333333333;
  font-family: sans-serif;
  text-align: center;
  margin-top: 100px;
  height: 200px;
}

.monitor-icon {
  position: absolute;
  z-index: 333;
  background-color: transparent;
  width: 20px;
  height: 19px;
}

.monitor {
  &:hover {
    color: #3c3c3b;
  }
}

.card-group {
  &__icon {
    &:hover {
      .monitor {
        color: #3c3c3b;
        &:hover {
          color: #3c3c3b;
        }
      }
    }
  }
}

.tooltip-inactive {
  &__container {
    position: absolute;
    z-index: 900;
    width: 337px;
    background: #ffffff;
    border: 0.5px solid #cfd2d3;
    box-shadow: 0px 10px 18px rgba(0, 0, 0, 0.08);
    border-radius: 8px;
    padding: 10px;
    top: 100px;
    left: 52px;
  }
  &__content {
    width: 100%;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    .user-bar__avatar {
      padding-top: 3px;
    }
    .user {
      display: flex;
      width: 100%;
      align-items: center;
      vertical-align: middle;
      max-width: 289px;
      margin-left: 29px;
      .user__name {
        margin-top: 0px;
      }
      &__booking__line {
        margin-top: 17px;
        margin-bottom: 17px;
        margin-left: 31px;

        border: 0.5px solid #cfd2d3;
      }
    }
  }
  &__title {
    display: flex;
    align-items: center;
  }
  &__subtitle {
    display: flex;
    margin-top: 17px;
    margin-left: 42px;
    align-items: center;
    max-width: 294px;

    .user__booking__item {
      background: rgba(250, 181, 38, 0.12);
      color: #fab526;
    }
  }
  &__icon {
    margin-right: 8px;
  }
}
.card-owner {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.workspace__card--part-inactive {
  .ctrl-button--icon--inactive {
    background-color: white !important;
  }
}
.workspace__card--inactive {
  .ctrl-button--icon--inactive {
    background-color: #fafafa !important;
  }
}
