.my-reservations {
  .edit-btn {
    position: relative;
  }

    .my-reservation__title-group {
      display: flex;
      align-items: center;
      gap: 12px;
    }

    .my-reservation__main-title {
      font-family: 'Montserrat';
      font-weight: 700;
      font-size: 21px;
      line-height: 123%;
    }

    .my-reservation__sub-title {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 21px;

      color: #3C3C3B;

      opacity: 0.48;
    }

  .my-reservation__filters--mobile {
    display: none;
  }

  .my-reservation__filters--disabled {
    display: none ;
  }

  .my-reservation__filters--reservation {
    display: block;
  }
}

@media (max-width: 420px) {
  .my-reservations {
    .my-reservation__search-title-group {
      flex-direction: column;
      display: block;
    }

    .my-reservation__search-title-group .my-reservation__main-title {
      font-size: 17px;
    }

    .my-reservation__search-settings {
      flex-direction: row;
      flex-wrap: nowrap;
    }

    .edit-btn--is-search {
      display: none;
    }
    .search-page__content {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
}
