.delete-modal {
  opacity: 0;
  visibility: hidden;
  transition: 0.2s opacity, 0.2s visibility;
  position: fixed;
  bottom: 6%;
  right: 6%;
  filter: drop-shadow(0px 10px 18px rgba(0, 0, 0, 0.08));

  &--active {
    visibility: visible;
    opacity: 1;
  }

  .delete-modal__container {
    display: flex;
    padding: 15px;
    gap: 43px;
    border: 0.5px solid #CFD2D3;
    border-radius: 8px;
    background: #FFFFFF;
  }

  .delete-modal__places {
    display: flex;
    flex-direction: column;
    gap: 4px;
  }

  .delete-modal__count {
    color: rgba(60, 60, 59, 0.64);
    font-size: 13px;
  }

  .form-edit-card__button {
    width: auto !important;
    background: rgba(228, 0, 58, 0.12);
    padding: 6px;
    border-radius: 4px;

    &:hover {
      background: rgba(228, 0, 58, 0.2);
    }
  }

  .ctrl-button__text {
    margin: 0;
    color: #E4003A;
    font-size: 12px;
  }
}

.delete-modal + .modal-form__background {
  .form-edit-card__button {
    background: rgba(228, 0, 58, 0.12);
    color: #E4003A;

    &:hover {
      background: rgba(228, 0, 58, 0.2);
    }
  }
}

@media (max-width: 420px) {
  .delete-modal {
    right: 50%;
    margin-right: -138.5px;

    .delete-modal__container {
       gap: 20px;
     }
  }
}

