.validation {
  width: 300px;
  align-content: center;
  display: flex;
  margin-top: 78px;
  margin-bottom: 12px;
  align-items: flex-start;
  visibility: hidden;
  opacity: 0;
  transition: 0.2s opacity, 0.2s visibility;

  --error-inner: #E4003A;
  --error-outer: #FFFFFF;

  &--active {
    visibility: visible;
    opacity: 1;
  }
}

.time__container-text {
  font-size: 13px;
}

.time__container-text--empty {
  color: #E4003A !important;
  overflow-wrap: break-word;
}

