.authtorisation {
  display: flex;
  min-height: 100vh;
  background-color: #fff;

  &::after {
    content: '';
    background-image: url('../../assets/images/cover/cover.svg');
    background-repeat: no-repeat;
    background-size: cover;
    width: 706px;
    flex-shrink: 0;
  }

  .time-line-checkbox__custom + label::before {
    width: 18px;
    height: 18px;
    border-color: #CFD2D3;
    border-radius: 4px;
  }

  .icon__select-checkbox {
    width: 100%;
    display: flex;
    top: 30%;
    left: 0;
  }

  .time-line {
    display: flex;
    height: auto;
    gap: 11px;
    align-items: center;
    margin-bottom: 51px;
    background: transparent;

    &-title {
      font-size: 14px;
    }

    &:hover {
      border-color: transparent;
    }
  }

  &__box {
    display: flex;
    width: 100%;
    padding: 20px;
  }

  &__group {
    max-width: 375px;
    margin: auto;
  }

  &__logo-block {
    margin-bottom: 72px;
  }

  &__description {
    margin-bottom: 48px;
  }

  &__title {
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 21px;
    line-height: 125%;
    margin-bottom: 12px;
  }

  &__text {
    font-size: 14px;
    line-height: 130%;
    color: rgba(60, 60, 59, 0.48);
  }
}

.logo {
  display: inline-flex;
  align-items: baseline;
  font-family: 'Proxima-Nova';
  font-weight: 900;
  font-size: 19px;
  line-height: 125%;
  letter-spacing: 0.202em;
  text-transform: uppercase;

  &::before {
    content: '';
    width: 4px;
    height: 4px;
    background-color: #00a1df;
    border-radius: 50%;
    margin-right: 5px;
    flex-shrink: 0;
  }

  &__decor {
    color: #00a1df;
  }

  &__text {
    color: #3c3c3b;
  }
}

.form-authtorisation__login {
  margin-bottom: 39px;
}

.form-authtorisation__password {
  margin-bottom: 26px;
}

.form-item {
  position: relative;

  &__label {
    display: block;
    font-size: 13px;
    line-height: 120%;
    margin-bottom: 8px;
  }

  &__input {
    padding: 15px 12px 12px;
  }
}

.input {
  font-family: 'Roboto';
  background-color: #f8f7f7;
  border: 0.5px solid #cfd2d3;
  border-radius: 8px;
  color: inherit;
  font-size: 16px;
  line-height: 120%;
  width: 100%;
  transition-property: border-color, color, background-color;
  transition-duration: 0.25s;
  transition-timing-function: ease;
}

.input:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(0, 161, 223, 0.18);
  border-color: #00a1df;
  background-color: #fff;
}

.input::-webkit-input-placeholder {
  color: #cfd2d3;
}

.input::-moz-placeholder {
  color: #cfd2d3;
}

.input::-ms-input-placeholder {
  color: #cfd2d3;
}

.input::placeholder {
  color: #cfd2d3;
}

.ctrl-button {
  width: 100%;
  padding: 12px;
  border-radius: 8px;
  background-color: #00a1df;
  color: #fff;

  &:disabled {
    background-color: #f8f7f7;
    color: #cfd2d3;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__text {
    font-family: 'Roboto';
  }
}

@media (any-hover: hover) {
  .ctrl-button:hover {
    background-color: #66c7ec;
  }
}
