.sidebar {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  width: 248px;
  z-index: 100;
  background-color: #fff;
  border-right: 1px solid rgba(207, 210, 211, 0.5);

  &__header {
    padding: 32px 24px;
  }

  &__title {
    display: flex;
    align-items: center;
    font-size: 17px;
    line-height: 120%;
    padding: 20px 20px 20px 34px;

    &-icon {
      margin-right: 14px;
      --icon-inner: #F8F7F7;
      --icon-outer: #CFD2D3;
    }
  }

  &__rights {
    margin-top: auto;
  }
}

@media (max-width: 576px) {
  .sidebar__content {
    .compensation-sidebar {
      display: none;
    }
  }
}
