.Toastify__toast {
  backdrop-filter: blur(96px);
  border-radius: 8px;
  padding: 10px 20px;
  color: rgba(60, 60, 59, 1);
  right: 98px;
  min-width: max-content;
}

.Toastify__toast.Toastify__toast--error {
  background-color: rgba(228, 0, 58, 0.12);
  border: 0.5px solid #e4003a;
}

.Toastify__toast.Toastify__toast--success {
  background: rgba(0, 167, 131, 0.12);
  border: 0.5px solid #00a783;
}

.Toastify__close-button {
  color: rgba(60, 60, 59, 1);
}
.Toastify__progress-bar {
  background-color: rgba(228, 0, 58, 0.12);
  opacity: 1;
  color: #3c3c3b;
}
.Toastify__toast-body {
  color: #3c3c3b;
  font-size: 13px;
  line-height: 15px;
  display: flex;
  align-items: top;
}
.Toastify__toast-body div::first-line {
  font-size: 16px;
  line-height: 19px;
}

@media (max-width: 480px) {
  .Toastify__toast {
    right: 0px;
  }
}
