.my-reservations__filter {
  padding: 39px clamp(26px, (26px + (100 - 26) * ((100vw - 1820px) / (1920 - 1820))), 100px) 32px;
  background-color: #f8f7f7;
}

.my-reservations__settings {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  grid-gap: 21px;
}

.main-title {
  font-family: 'Montserrat';
  font-weight: 700;
  font-size: 21px;
  line-height: 123%;
}

.my-reservations__btns {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  grid-gap: 20px;
}

.edit-btn {
  min-width: 164px;
  padding: 9px 24px 10px;
  background-color: #f8f7f7;
  border-width: 0.5px;
  border-style: solid;
  border-color: #cfd2d3;
  border-radius: 8px;
  font-family: 'Roboto';
  font-size: 16px;
  line-height: 119%;
  color: #3c3c3b;
  transition-property: border-color, background-color, color;
  transition-delay: 0.13s;
  transition-duration: 0.13s;
  transition-timing-function: linear;
}

.edit-btn:hover {
  outline: 2px solid rgba(0, 161, 222, 0.18);
}

.edit-btn--active {
  border-color: rgba(0, 161, 223, 0.12);
  background-color: rgba(0, 161, 223, 0.12);
  color: #00a1df;
}

.filters-btn {
  position: relative;
  display: none;
  align-items: center;
  justify-content: center;
  border-width: 0.5px;
  border-style: solid;
  border-color: #cfd2d3;
  border-radius: 8px;
  background-color: #f8f7f7;
  --primary-color: #3c3c3b;
}

.filters-btn--active {
  border-color: rgba(0, 161, 223, 0.12);
  --primary-color: #00a1df;
}

.filters-btn--active::after {
  content: '';
  position: absolute;
  top: -3px;
  right: -5px;
  width: 8px;
  height: 8px;
  background-color: #e4003a;
  border-radius: 8px;
}

.my-reservation__filters {
  margin-top: 31px;
}

.my-reservation__wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(336px, 1fr));
  grid-gap: 4px;
}

.filter-box {
  position: relative;
  padding: 20px;
  background-color: #fff;
  border-width: 1px;
  border-style: solid;
  border-color: #cfd2d3;
  border-radius: 4px;
}

.filter-box__name {
  font-weight: 500;
  font-size: 12px;
  line-height: 117%;
  margin-bottom: 12px;
}

.filter-box__period {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.filter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-column-gap: 5px;
  border-width: 1px;
  border-style: solid;
  border-color: #cfd2d3;
  border-radius: 8px;
  color: #cfd2d3;
  padding: 15px 10px 14px;
  cursor: pointer;
}

.filter--active {
  background-color: rgba(0, 161, 222, 0.06);
  border-color: #00a1df;
  color: #00a1df;
}

.filter-box__filter {
  width: 100%;
}

.filter-box__period-icon {
  flex-shrink: 0;
  margin: 12px;
}

.filter__text {
  white-space: nowrap;
  overflow-x: auto;
  line-height: 119%;
}

.filter__text::-webkit-scrollbar {
  width: 0px;
}
.filter__text::-webkit-scrollbar:horizontal {
  height: 3px;
  border-radius: 20px;
}
.filter__text::-webkit-scrollbar-thumb:horizontal {
  background-color: #00a1df;
  border-radius: 20px;
}

.filter__arrow {
  flex-shrink: 0;
  --settings-inner: #3c3c3b;
}

.filter__close {
  flex-shrink: 0;
  --icon-path-primary: #00a1df;
}

.filter-box__location-filter,
.filter-box__search-filter {
  position: absolute;
  left: 0;
  top: 103%;
}

.filter-box__location-filter {
  border-width: 1px;
  border-style: solid;
  border-color: #cfd2d3;
  border-radius: 8px;
}

.checkbox-inner {
  position: relative;
  display: block;
  margin-right: 12px;
}

.checkbox-inner__field {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
}

.checkbox-inner__field:checked + .custom-checkbox::before {
  display: flex;
  align-items: center;
  justify-content: center;
  content: url('../../assets/images/location-filter/checked.svg');
  background-color: #00a1df;
  border-color: #00a1df;
}

.checkbox-inner__custom-checkbox {
  padding: 15px;
  font-size: 14px;
  border-radius: 8px;
}

.checkbox-inner__custom-checkbox:hover {
  background-color: rgba(207, 209, 212, 0.12);
  color: #00a1df;
}

.custom-checkbox {
  display: flex;
  position: relative;
  align-items: center;
  cursor: pointer;
}

.custom-checkbox::before {
  position: relative;
  content: '';
  margin-right: 15px;
  width: 18px;
  height: 18px;
  border-width: 1px;
  border-style: solid;
  border-color: #cfd2d3;
  border-radius: 4px;
}

.action-btn {
  display: none;
  padding: 14px 10px;
  background-color: #00a1df;
  color: #fff;
  line-height: 125%;
  border-radius: 8px;
}

.action-btn:hover {
  background-color: #66c7ec;
}
.action-btn:focus {
  background-color: #66c7ec;
  outline: 2px solid #00a1df;
}
.action-btn:active {
  background-color: #18799e;
  outline-style: none;
}
.action-btn:disabled {
  background-color: #96d7f2;
}

.rooms {
  padding: 48px clamp(26px, (26px + (100 - 26) * ((100vw - 1820px) / (1920 - 1820))), 100px);
}

.my-reservation__filter-mode::after {
  content: '';
  display: block;
  position: absolute;
  top: -4px;
  right: -5px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #e4003a;
  visibility: hidden;
  opacity: 0;
}

.my-reservation__edit::after,
.my-reservation__filter-mode::after {
  visibility: visible;
  opacity: 1;
}

// DatePicker Style
.my-reservation__filter-data .react-datepicker-popper,
.my-reservation__filter-time .react-datepicker-popper {
  top: 103% !important;
  padding-top: 0px !important;
  transform: initial !important;
}

.my-reservation__filter-data .react-datepicker-popper--from,
.my-reservation__filter-time .react-datepicker-popper--from {
  margin-left: 0px;
}

.my-reservation__filter-data .react-datepicker-popper--to,
.my-reservation__filter-time .react-datepicker-popper--to {
  right: 0 !important;
  left: initial !important;
}

.my-reservation__filter-data .react-datepicker__header__dropdown {
  padding-right: 0;
}

.my-reservation__filter-data .react-datepicker__year-dropdown {
  background-color: #fff;
  width: 60%;
  left: 17%;
}

.my-reservation__filter-data .react-datepicker__year-option {
  position: relative;
}

.my-reservation__filter-data .react-datepicker__year-option:first-of-type,
.my-reservation__filter-data .react-datepicker__year-option:last-of-type,
.my-reservation__filter-data .react-datepicker__year-option:first-of-type:hover,
.my-reservation__filter-data .react-datepicker__year-option:last-of-type:hover {
  background-color: #fff;
}

.my-reservation__filter-data .react-datepicker__year-option:first-of-type::after,
.my-reservation__filter-data .react-datepicker__year-option:last-of-type::after {
  content: url(../../assets/images/datepicker/arrow-down.svg);
  position: absolute;
  left: 50%;
  top: 50%;
  height: 19px;
}

.my-reservation__filter-data .react-datepicker__year-option:first-of-type::after {
  transform: translate(-50%, -50%);
}

.my-reservations__filter .react-datepicker__year-option:last-of-type::after {
  transform: translate(-50%, -50%) rotate(180deg);
}

.my-reservation__filter-data .react-datepicker__year-option:hover:not(.react-datepicker__year-option--selected_year) {
  background-color: #00a0df8b;
}

.my-reservation__filter-data .react-datepicker__year-option--selected_year,
.my-reservation__filter-time .react-datepicker__time-list-item--selected {
  background-color: #00a1df !important;
  color: #fff;
}

.my-reservation__filter-data .react-datepicker__year-option--selected {
  display: none;
}

.my-reservation__filter-time .react-datepicker {
  width: auto !important;
  padding: 9px 0px 9px 8px;
}

.my-reservation__filter-time .react-datepicker__header {
  display: none;
}

.my-reservation__filter-time .react-datepicker__time-list {
  scrollbar-width: thin !important;
}

.my-reservation__filter-time .react-datepicker__time-list::-webkit-scrollbar {
  width: 3px;
}

.my-reservation__filter-time .react-datepicker__time-list::-webkit-scrollbar-thumb {
  background-color: #3c3c3b;
  border-radius: 20px;
}

.my-reservation__filter-time .react-datepicker__time-list-item {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 7px;
  border-radius: 4px;
}

.my-reservation__filter-time .react-datepicker__time-list-item:hover:not(.react-datepicker__time-list-item--selected) {
  background-color: #00a0df8b !important;
  color: #fff;
}
// DatePicker Style

@import './responsive.scss';
