.user-profile {
  display: flex;
  flex-direction: column;
  padding: 20px;

  .user-profile__buttons {
    display: flex;

    .user-profile__button:last-child {
      margin-left: 20px;

      background: rgba(0, 167, 131, 0.12) ;
      color: #00A783 ;

      &:hover {
        background: rgba(0, 167, 131, 0.2);
      }
    }
  }

  .form-item__input--invalid {
    border-color: #E4003A;
    box-shadow: 0 0 0 2px rgb(228 0 58 / 18%);
  }
}

.user-profile__file-upload {
  display: flex;
  align-items: center;
  margin-top: 36px;
  margin-bottom: 24px;
  flex-direction: column;
}

.user-profile__file-upload .user-profile__avatar {
  width: 192px ;
  height: 192px ;
  position: relative;
  margin-bottom: 24px;
  border: 2px solid rgba(0, 161, 223, 0.32);
  box-shadow: inset 0 0 0 11px #fff;

  --user-profile-inner: rgba(0, 161, 223, 0.12);
  --user-profile-outer: #00a1df;
}

.user-profile__icon {
  top: 15%;
  left: 18%;
  position: absolute;
}

.user-profile__field {
  width: auto;

  margin-bottom: 30px;
}

.user-profile__input {
  height: 48px;
}

.user-profile__input.is-invalid {
  box-shadow: 0 0 0 2px rgba(228, 0, 58, 0.18) !important;
  border-color: #E4003A !important;
}

.user-profile__input.is-invalid:focus {
  box-shadow: 0 0 0 2px rgba(228, 0, 58, 0.18) !important;
  border-color: #E4003A !important;
}

.user-profile__button {
  @media (max-width: 575px) {
    height: auto !important;
    font-size: 16px !important;
  }
}

.user-profile__input-file--hidden {
  visibility: hidden;
}

.user-profile__input-file {
  width: 200px;
  height: 42px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 161, 223, 0.12);
  color: #00A1DF;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.2s background-color;

  &:hover {
    background: rgba(0, 161, 223, 0.08);
    color: #00A1DF;
  }
}

.user-profile__title {
  font-style: normal;
  font-weight: bold;
  font-size: 21px;
  line-height: 26px;
  margin-top: 12px;

  @media (max-width: 420px) {
    margin-top: 34px;
    align-self: center;
  }
}

.user-profile__image {
  width: 170px;
  height: 170px;
  position: absolute;
  top: 5%;
  left: 5%;
  border-radius: 50%;
}

.user-profile__image-schedule {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  box-shadow: none;

  @media (max-width: 380px) {
    width: 22px;
    height: 25px;
  }
}

.user-profile__image-header {
  width: 36px;
  height: 36px;
  border-radius: 50%;
}


