.user-table_wrapper {
  margin: 80px calc(100% - 90%);
  max-width: 1470px;
  .lock {
    &-opened {
      &-icon {
        display: none;
      }
    }
    &-closed {
      &-icon {
        display: none;
      }
    }
  }
  .table-button-active {
    border: 0.5px solid rgba(0, 161, 223, 0.12);
    box-sizing: border-box;
    border-radius: 8px;
    background: rgba(0, 161, 223, 0.12);
    color: #00a1df;
    max-width: 186px;
    min-width: 69px;
    .ctrl-button__text {
      margin-right: 0px;
    }
  }
  .table-button-block {
    background: rgba(228, 0, 58, 0.12);
    border: 0.5px solid rgba(228, 0, 58, 0.12);
    box-sizing: border-box;
    border-radius: 8px;
    color: #e4003a;
    max-width: 186px;
    min-width: 69px;
    .ctrl-button__text {
      margin-right: 0px;
    }

    &:hover {
      background-color: #e400398e !important;
    }
  }
}

@media (max-width: 920px) {
  .user-table_wrapper {
    min-width: 623px;
    .table-button-active {
      display: none;
    }
    .table-button-block {
      display: none;
    }

    .lock {
      &-opened {
        &-icon {
          display: block;
        }
      }
      &-closed {
        &-icon {
          display: block;
        }
      }
    }
  }
}
