.schedule {
  display: flex;
  border-left: 2px solid #f8f7f7;
  flex-grow: 1;

  &__reservation {
    width: 100%;
    &__info {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      height: 60px;
      max-width: 1243px;
      display: flex;
      justify-content: flex-end;
      align-items: center;
    }
  }
  .info-schedule {
    display: flex;
    justify-content: center;
    &__btn {
      max-width: 212px;
      background: rgba(0, 161, 223, 0.12);
      border-radius: 8px;
      color: #00a1df;
      margin-left: 32px;
    }
    &--addition {
      color: hsla(60, 1%, 23%, 0.64);
    }
  }
  //Schedule-Dashboard
  &-dashboard {
    padding: 10px 20px;
    border-bottom: 2px solid #f8f7f7;

    &__wrapper {
      display: flex;
      align-items: center;
      flex-wrap: wrap;

      @media (max-width: 420px) {
        margin-top: 50px;
      }
    }

    &__place {
      position: relative;
      margin-right: 65px;

      &-btn {
        display: flex;
        align-items: center;
        background-color: transparent;
        font-family: 'Roboto';
        font-weight: 500;

        &-icon {
          --icon-inner: rgb(220, 240, 251);
          --icon-outer: #00a1df;
        }

        &-arrow {
          --arrow-down-out: rgba(0, 161, 222, 0.05);
          --arrow-down-inner: #3c3c3b;
        }
      }
    }

    &__filter {
      position: absolute;
      left: 0;
      top: 171%;
    }

    &__current-location {
      margin-left: 10px;
      margin-top: 2px;
      margin-right: 10px;
    }

    &__current-room {
      font-size: 12px;
      color: #cfd2d3;
      margin-right: 3px;
    }

    &__current-place {
      font-size: 18px;
      color: #3c3c3b;
    }

    &__plus {
      display: none;
      background-color: transparent;
      margin-left: auto;

      &-icon {
        display: block;
      }
    }

    &__box {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-grow: 1;
      flex-wrap: wrap;
    }

    &__date {
      display: flex;
      align-items: center;
      margin: auto;

      @media (max-width: 1650px) {
        margin: 0;
      }

      &-btn {
        min-width: 111px;
      }
    }

    &-date-btn {
      background-color: #fff;
      font-family: 'Montserrat';
      text-align: center;
      font-weight: 500;
      border: 0.5px solid #cfd2d3;
      border-radius: 8px;
      padding: 10px 5px 9px;
      color: #3c3c3b;

      &:hover {
        box-shadow: 0 0 0 2px rgba(0, 161, 223, 0.18);
      }
    }

    &__calendar {
      margin-left: 10px;
    }

    &__select-time {
      display: flex;

      &-btn {
        width: 111px;
      }

      &-btn:not(:last-child) {
        margin-right: 12px;
      }
    }
  }

  //Schedule-Weeks
  &-weeks {
    display: flex;
    border-bottom: 2px solid #f8f7f7;

    &__place {
      max-width: 80px;
      width: 100%;
      text-align: center;
      border-right: 2px solid #f8f7f7;

      &-name {
        font-family: 'Montserrat';
        font-weight: 700;
        font-size: 21px;
        color: #00a1df;
        border-bottom: 2px solid #f8f7f7;
        padding: 27px 5px;
        height: 80px;
      }

      &-hour {
        font-weight: 500;
        padding: 10px 3px;
        height: 40px;

        &:not(:last-child) {
          border-bottom: 2px solid #f8f7f7;
        }
      }
    }

    &__content {
      width: 100%;
    }

    &__header {
      display: flex;

      &-day {
        display: flex;
        height: 80px;

        &:not(:last-child) {
          border-right: 2px solid #f8f7f7;
        }

        &-wrapper {
          margin: auto;
        }

        &-name,
        &-data,
        &-month {
          font-weight: 500;
          text-transform: capitalize;
        }

        &-name {
          margin-bottom: 2px;
          color: #cfd2d3;
        }

        &-data {
          margin-right: 4px;
        }

        &-month {
          display: inline-block;
        }
      }
    }

    &__booking {
      position: relative;
      display: flex;

      &-line {
        height: 40px;

        &::after {
          content: '';
          border-bottom: 2px solid #f8f7f7;
          position: absolute;
          width: 100%;
          margin-top: -1px;
        }
      }

      &-wrapper-column {
        display: flex;
        flex-grow: 1;
      }

      &-column {
        &:not(:last-child) {
          border-right: 2px solid #f8f7f7;
        }
      }
    }
  }

  .delete-schedule + .modal-form__background {
    .modal-form__container {
      max-width: 422px;
      display: flex;
      flex-direction: column;
      padding: 20px;
      border-radius: 14px;
      font-family: 'Montserrat';
    }

    .modal-form__title {
      position: relative;
      padding: 0;
      margin-bottom: 18px;

      font-weight: 700;
      font-size: 21px;
      line-height: 26px;
    }

    .modal__wrapper {
      padding: 0;

      p {
        margin-bottom: 32px;
      }

      .control_buttons {
        display: flex;
        justify-content: space-between;
      }

      .control_buttons button {
        max-width: 182px;
      }
    }
  }
}

.schedule-weeks__header-day,
.schedule-weeks__booking-column {
  width: 100%;
  padding-left: 5px;
  padding-right: 5px;
}

.schedule-dashboard-date-btn--blue {
  background-color: rgba(0, 161, 222, 0.06);
  border-color: #00a1df;
  color: #00a1df;
}

.time-line {
  position: relative;
  width: 100%;
  background-color: rgba(0, 161, 223, 0.12);
  border: 1px solid transparent;
  border-radius: 16px;
  height: 200px;
  color: #00a1df;

  @media (any-hover: hover) {
    &:hover {
      border-color: #00a1df;
    }
  }

  &__inner {
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100%;
  }

  &__user {
    width: 32px;
    height: 32px;
    display: none;
    border: 1px solid #fff;
    border-radius: 50%;
    object-fit: cover;
    background-color: brown;
  }

  &__checkbox-wrapper {
    padding-top: 4px;
    padding-bottom: 4px;
    margin-left: auto;
    display: none;
  }

  &__checkbox {
    display: block;
    width: 24px;
    height: 24px;

    &:not(:last-child) {
      margin-bottom: 16px;
    }

    &-all {
      position: absolute;
      right: 0;
      top: -21px;
      width: 15px;
      height: 15px;
      display: none;
    }
  }
}

.time-line--foreign {
  color: #e4003a;
  background-color: rgba(228, 0, 58, 0.12);
  &:hover {
    border-color: #e4003a;
  }
}

.time-line__user--active {
  display: block;
}

.time-line-checkbox {
  background-color: #fff;
  border-radius: 50%;
  border: 1px solid #00a1df;
}

.time-line-checkbox__element {
  height: 20px;
  width: 20px;
}

.time-line__ceil {
  height: 39.5px;
  display: flex;
  position: relative;
  justify-content: right;
  align-items: center;

  @media (max-width: 420px) {
    justify-content: center;
  }
}

.time-line__ceil .user-bar__avatar {
  width: 30px;
  height: 30px;
}
.time-line--inactive {
  background: #fff5df;
  &:hover {
    border: 1px solid #fab526;
  }
}
.schedule {
  .user-bar__avatar {
    &--inactive {
      border: 1px solid #fab526;
      color: #fab526;
      background: #fff5df;
      width: 30px;
      height: 30px;
      font-weight: 500;
      border-radius: 50%;
      text-align: center;
      line-height: 29px;
      color: #fab526;
    }
  }
}

.time-line__avatar {
  width: 32px !important;
  height: 32px !important;
  margin-right: auto;

  .time-line__avatar-text {
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffff !important;
    border-radius: 50%;

    @media (max-width: 380px) {
      width: 22px;
      height: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  @media (max-width: 420px) {
    margin-left: auto;
  }
}

.time-line-checkbox--disabled {
  display: none;
}

.time-line-checkbox--clicked {
  background-color: #00a1df;
}

.time-line-checkbox__custom {
  z-index: -1;
  opacity: 0;
  position: absolute;
}

.time-line-checkbox__custom-select-all {
  z-index: -1;
  opacity: 0;
  position: absolute;
}

.time-line-checkbox__custom + label {
  position: relative;
  display: inline-flex;
  user-select: none;
}

.time-line-checkbox__custom-select-all + label {
  user-select: none;
}

.time-line-checkbox__custom + label::before {
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  background-color: #fff;
  border-radius: 50%;
  border: 1px solid #00a1df;
}

.time-line-checkbox__custom-select-all + label::before {
  content: '';
  display: inline-block;
  width: 15px;
  height: 15px;
  background-color: #fff;
  border-radius: 50%;
  border: 1px solid #00a1df;
}

.time-line-checkbox__custom:checked + label::before {
  border-color: #00a1df;
  background-color: #00a1df;
}

.time-line-checkbox__custom-select-all:checked + label::before {
  border-color: #00a1df;
  background-color: #00a1df;
}

.time-line__checkbox-wrapper--active,
.time-line__checkbox-all--active {
  display: none;
}

.disable {
  display: none !important;
}

.time-line--active {
  border: 1px solid currentColor;
}

.icon__select-all-checkbox {
  display: none;
  position: absolute;
  top: 4px;
  right: 3px;
}

.icon__select-checkbox {
  position: absolute;
  top: 8px;
  right: 5px;
}

.time-line-checkbox--active {
  display: block;
}

.schedule-weeks__header-day--active {
  background: #e0f4fb;
}

.time-line__ceil-avatar {
  @media (max-width: 420px) {
    justify-content: center;
  }

  justify-content: space-between;
}

.time-line-checkbox-avatar {
  justify-content: right;
}

.schedule-dashboard__date-btn-margin {
  margin-right: 20px;

  @media (max-width: 330px) {
    margin: 0;
  }
}

.schedule-dashboard-date-btn--active {
  background: rgba(0, 161, 223, 0.12);
  border: 0.5px solid #00a1df;
  color: #00a1df;
}

.schedule-btn--active {
  background: rgba(0, 161, 223, 0.12);
  border: 0.5px solid #00a1df;
  color: #00a1df;
}

.schedule-week__users {
  background-color: #fff;
  border-radius: 8px;
  border: 1px solid rgba(207, 210, 211, 0.5);
  box-shadow: 0px 10px 18px rgba(0, 0, 0, 0.08);
  padding: 11px;
}
.user__booking__item {
  &--inactive {
    background: rgba(250, 181, 38, 0.12);
    color: #fab526;
  }
}
@media (max-width: 1580px) {
  .info-schedule {
    flex-direction: column;
    flex-wrap: wrap;
  }
}

.time-line {
  &--inactive {
    background: rgba(250, 181, 38, 0.12) !important;
    &:hover {
      border-color: #fab526;
    }
  }
}
@media (max-width: 1308px) {
  .info-schedule {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

@media (max-width: 973px) and (min-width: 778px) {
  .info-schedule {
    flex-direction: column;
    flex-wrap: wrap;
  }
}
