@media (max-width: 992px) {
  .my-reservations {
    margin-top: 62px;
  }
  .rooms {
    padding-right: 89px;
    padding-left: 89px;
  }
}

@media (max-width: 767px) {
  .my-reservations__settings {
    flex-direction: column;
    align-items: initial;
  }
  .main-title {
    font-size: 17px;
  }
  .filters-btn {
    display: flex;
  }
  .my-reservations .back {
    display: block;
  }
  .my-reservation__filters {
    display: block !important;
    z-index: 9999999999999;
    background-color: #F8F7F7;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    margin-top: 0;
  }
  .my-reservation__filters--disabled {
    display: none !important;
  }
  .my-reservation__wrapper {
    display: flex;
    flex-direction: column;
    height: calc(100vh - 50px);
    overflow-y: auto;
  }
  .filter-box {
    border-radius: 0;
    border-left-style: none;
    border-right-style: none;
  }
  .filter-box:first-child {
    border-top-style: none;
  }
  .filter-box__period {
    flex-direction: column;
  }
  .filter-box__period-icon {
    transform: rotateZ(90deg);
  }
  .my-reservation__filter-place {
    margin-bottom: auto;
  }
  .my-reservations .action-btn {
    display: block;
    margin: 32px 20px;
  }
  .filter-box__location-filter, .my-reservations .filter-box__search-filter {
    position: fixed;
    top: 0;
    right: 0;
    border-style: none;
    border-radius: 0;
  }
  .location-filter {
    padding: 0;
  }
  .location-filter__wrapper {
    height: calc(100vh - 162px);
  }
  .my-reservations .search-filter__field-box {
    margin: 14px 20px;
  }
  .my-reservations .search-filter__field {
    border: 0.5px solid #CFD2D3;
    border-radius: 8px;
  }
  .my-reservations .search-filter__field:focus {
    border: 0.5px solid #00A1DF;
    box-shadow: 0 0 0 2.5px rgba(0, 161, 222, 0.18);
  }
  .my-reservations .search-filter__field:focus-visible {
    outline-style: none;
  }
  .my-reservations .search-filter__content {
    height: calc(100vh - 263px);
  }
  .rooms {
    padding-left: 20px;
    padding-right: 20px;
  }
  .my-reservations .react-datepicker-popper {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media (max-width: 576px) {
  .reservation-cards {
    grid-template-columns: 1fr;
  }
  .my-reservation__filter-data .react-datepicker-popper, .my-reservation__filter-data .react-datepicker__month-container,
  .my-reservation__filter-data .react-datepicker__month, .my-reservation__filter-data .react-datepicker__header,
  .my-reservation__filter-data .react-datepicker__day-names {
    width: 100%;
  }
  .my-reservation__filter-data .react-datepicker {
    width: 100% !important;
  }
  .my-reservation__filter-data .react-datepicker__month {
    margin: 0;
  }
}