.filter_wrapper {
  background: #ffffff;
  border: 0.3px solid #cfd2d3;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 20px;
  margin-right: 4px;
  margin-bottom: 4px;
  max-width: 393px;
  width: 356px;
  .input {
    padding: 14px 14px 13px;
  }
  .filter_title {
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    width: max-content;
    margin-bottom: 12px;
  }
}

.filter-search__input {
  background: rgba(0, 161, 223, 0.06);
  border-radius: 8px;
}
@media (max-width: 1166px) and (min-width: 1125px) {
  .filter_content {
    margin: 80px 10%;
    .header__form-search {
      display: block !important;
    }
  }
  .filter_wrapper {
    width: 100%;
    max-width: 100% !important;
  }
  .form-search {
    max-width: 100% !important;
  }
}
@media (max-width: 1124px) {
  .filter_content {
    margin: 80px 4%;
  }
  .filter_wrapper {
    width: 100%;
    max-width: 100% !important;
  }
  .form-search {
    max-width: 100% !important;
  }
}
@media (max-width: 992px) {
  .filter_content {
    margin: 36px 18px;
    .header__form-search {
      display: block !important;
    }
  }
  .filter_wrapper {
    margin-right: -1px;
  }

  .filters_wrapper {
    margin-top: 49px;
  }
}
@media (max-width: 765px) {
  .filter_wrapper {
    width: 100%;
    max-width: 100% !important;
  }
  .form-search {
    max-width: 100% !important;
  }
  .filter_wrapper {
    &:first-child {
      margin-right: 0px;
    }
  }
}
@media (max-width: 650px) {
  .filters_wrapper {
    text-align: left;
  }
}
@media (max-width: 621px) {
  .filters_wrapper {
    display: flex;
    text-align: left;
    align-items: center;
  }
  .filters__button {
    margin-top: 96px;
    margin-bottom: 32px;
  }
}

@media (max-width: 360px) {
  .user-table_wrapper {
    margin: 32px 4%;
  }
}
