.tag__wrapper {
  max-width: 167px;
  height: 27px;
  border: 0.5px solid rgba(0, 161, 223, 0.5);
  box-sizing: border-box;
  border-radius: 4px;
  padding: 4px 4px 4px 8px;
  display: flex;
  align-items: center;
  margin: 0px 8px 8px 0px;
  cursor: pointer;
  &--active {
    background: #00a1df;
    color: #ffffff;
  }
}

.delete-tag_icon {
  margin-left: 9px;
}
