.workspace__schema {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  align-self: baseline;
  align-items: center;
  cursor: pointer;
  padding: 20px;
  margin: -20px;
  &-text {
    margin-left: 8px;
  }
}

.modal {
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  opacity: 0;
  visibility: hidden;
  justify-content: flex-end;
  top: 0;
  left: 0;
  pointer-events: none;
  transition: all 0.3s linear;

  &__container {
    display: flex;
    flex-direction: column;
    margin-top: 10em;
    margin-right: 5em;
  }

  &__title {
    padding: 20px;
    font-size: 21px;
    border-bottom: 1px solid #cfd2d3;
  }

  &__img {
    border-radius: 10px;
    padding: 24px;
  }

  &__group {
    background: #ffffff;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    box-shadow: 0px 10px 18px rgba(0, 0, 0, 0.08);
  }
}

.modal.is-active {
  opacity: 1;
  visibility: visible;
  background-color: rgba(60, 60, 59, 0.2);
  z-index: 3;
}

@media (max-width: 1440px) {
  .modal {
    &__container {
      margin-top: 150px;
      margin-right: 15px;
    }
  }
}

@media (max-width: 1280px) {
  .modal {
    &__img {
      width: 820px;
    }
  }
}

@media (min-width: 768px), (max-width: 1199px) {
  .workspace__header {
    flex-wrap: wrap;
  }
}

@media (max-width: 1024px) {
  .modal {
    justify-content: center;

    &__container {
      margin-top: 190px;
      margin-left: auto;
    }

    &__img {
      width: 644px;
    }
  }

  .is-active {
    justify-content: center;
  }
}

@media (max-width: 768px) {
  .modal {
    &__container {
      margin-top: 233px;
      margin-right: auto;
      margin-left: auto;
    }
  }
  .modal.is-active {
    justify-content: center;
  }
}

@media (max-width: 540px) {
  .modal {
    &__img {
      width: 480px;
    }
  }
}

@media (max-width: 425px) {
  .modal {
    &__container {
      margin-right: 0;
      margin-left: 0;
      margin-top: 212px;
    }

    &__img {
      width: 400px;
    }

    &__group {
      border-radius: 10px;
      pointer-events: all;
      overflow-x: auto;
      overflow-y: auto;
      max-width: 100vw;
      max-height: 70vh;
    }
  }

  .modal.is-active {
    justify-content: center;
  }
}

@media (max-width: 375px) {
  .modal__img {
    width: 350px;
  }
}

@media (max-width: 320px) {
  .modal__img {
    width: 300px;
  }
}
