.workspace__group {
  .add-button {
    padding: 0px;
    border: none;
    color: inherit;
    background-color: transparent;
    left: 0px;
    margin-top: 0px;
    margin-left: 0px;
    margin-right: 0px;
    color: #00a1df;
    border: 0.5px solid #cfd2d37e;
    border-radius: 8px;
    &__icon {
      width: 278px;
      height: 278px;
    }
    &:hover {
      color: #00a1df;
      border-color: #00a1df;
      .add use {
        color: #00a1df;
      }
    }
    svg rect {
      fill: inherit;
      stroke: inherit;
    }
    .add {
      width: 60px;
      height: 216px;
    }
    color: #00a1df;
    .d {
      fill: midnightblue;
    }
  }

  @media (max-width: 768px) {
    .add-button {
      margin-bottom: 20px;
    }
  }
}
