.card-tool__wrapper-item {
  width: 40px;
  height: 24px;
  position: relative;
}

.card-tool__wrapper-icon {
  padding-right: 4px;
  padding-right: 4px;
  --settings-out: #00a1df;
  --settings-inner: #dcf0fb;
}
.card-tool__wrapper-check {
  position: absolute;
  top: 0px;
  right: 0px;
}

.card-tool__wrapper-value {
  position: absolute;
  right: 2px;
  top: 5px;
}

.monitor {
  fill: #f8f7f7;
  stroke-linecap: round;
}

.add {
  color: #cfd2d3;
}

.checkMarkRed {
  color: #e4003a;
}

.checkMarkGreen {
  color: #00a783;
}

.keyboard-icon {
  width: 32px;
}

.edit-icon:hover {
  color: #00a1df !important;
}
.delete-icon:hover {
  color: rgba(228, 0, 58, 1) !important;
}

.card-tool__wrapper-value {
  left: 50px;
  top: 130px;
}

.action-icon {
  display: flex;
}
.lock-closed-icon {
  color: #8a8a89;
  stroke-opacity: 0.1;
}
.lock-closed-icon:hover {
  color: #e4003a;
  stroke-opacity: 0.1;
}
.lock-opened-icon {
  color: #8a8a89;
}
.lock-opened-icon:hover {
  color: #00a783;
}

.card__owner {
  height: 36px;
  background-color: aqua !important;
}
.bottom_card {
  margin-top: 0px;
  padding-top: 0px;
  margin-top: 0px !important;
}
