.filters_wrapper {
  background: #f8f7f7;
  width: 100%;
  height: max-content;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  display: flex;
  text-align: right;
  flex-direction: column;
  min-height: 169px;
  .filter_wrapper {
    position: relative;
    min-width: 319px;
  }
  .input {
    background: #ffffff;
    &::placeholder {
      max-width: 261px;
    }
  }
  .select__input--selected {
    border: 0.5px solid #00a1df !important;
    background-color: hsla(197, 100%, 44%, 0.06) !important;
    &::placeholder {
      color: #00a1df !important;
    }
  }

  .filter-search__icon-search {
    position: absolute;
    right: 12px !important;
    top: 12px !important;
    &--none {
      display: none;
    }
  }
  .form-search__input {
    padding-left: 14px;
    height: 48px;
  }
  .checkbox-wrapper {
    position: absolute;
    margin-top: -18px;
    margin-top: 4px;
    z-index: 888888888;
    padding: 12px;
    background: #ffffff;
    border: 0.5px solid #cfd2d3;
    box-sizing: border-box;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);
    border-radius: 8px;
    background: #ffffff;
    border: 0.5px solid #cfd2d3;
    box-sizing: border-box;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);
    border-radius: 8px;
    width: max-content;
    right: 20px;
    bottom: auto;
  }
  .checkbox-element {
    display: flex;

    padding: 15px 12px;
    &__label {
      margin-left: 10px;
    }
    &:hover {
      cursor: pointer;

      background: hsla(195, 4%, 82%, 0.12);
      border-radius: 8px;
    }
  }
  .custom-checkbox {
    display: none;
  }
  .checkbox_checkbox {
    width: 24px;
    height: 24px;
    background: #ffffff;
    border-radius: 4px;
    box-sizing: border-box;
  }
  .checkbox_checkbox::after {
    content: '';
    width: 24px;
    height: 24px;
    display: block;
    text-align: center;
    background: #ffffff;
    border-radius: 4px;
    border: 1px solid #cfd2d3;
    cursor: pointer;
  }
  .custom-checkbox:checked + .checkbox_checkbox::after {
    content: url(../../../assets/images/check-mark/checkbox.png);

    background: #00a1df !important;
    transform: scale(1);
  }
}
.filters_header {
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 0px 10%;
  .ctrl-button {
    width: 112px !important;
  }
}
.user-filter_title {
  margin-top: 51px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 21px;
  line-height: 26px;
}

.abort-filters__button {
  color: #00a1df;
  background-color: inherit;
  height: max-content;
  padding: 0px 0px;
  margin-top: 23px;
  width: 146px;
  font-size: 16px;
  margin-right: 20px;
  cursor: pointer;
  line-height: 19px;
  white-space: nowrap;
  &:hover {
    color: #00a1df;
    background-color: inherit;
    height: max-content;
    padding: 0px 0px;
    margin-top: 23px;
    width: 146px;
    font-size: 16px;
    line-height: 19px;
    white-space: nowrap;
  }
}

.filter__button {
  height: 40px;
  margin-top: 11px;
  background: rgba(0, 161, 223, 0.12) !important;
  &-wrapper {
    display: flex;
    flex-direction: row;
  }
  &--cancel {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    width: 146px;
    color: #00a1df;
  }
  .ctrl-button__text {
    color: #00a1df;
  }
}
.mark {
  position: absolute;
  z-index: 999999999;
  width: 8px;
  margin-top: -33px;
  margin-left: 84px;
  height: 8px;
  border-radius: 50%;
  background: #e4003a;
}
.filters__button {
  max-width: 112px;
  height: 40px;
  color: #3c3c3b !important;
  margin-right: 0px;
  background: #f8f7f7 !important;
  border: 0.5px solid #cfd2d3;
  box-sizing: border-box;
  border-radius: 8px;
  margin-top: 11px;
  &--wrapper {
    display: flex;
    height: 40px;
    margin-top: 32px;
    justify-content: space-between;
    flex-direction: row;
    .ctrl-button {
      padding: 0px !important;
    }
  }
}

.filter_content {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin: 36px 10%;
  max-width: 1187px;
  flex-wrap: wrap;
}
