.tag-monitor {
  &__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 8px;
    min-width: 126px;
    max-width: max-content;
    background: #f8f7f7;
    cursor: pointer;
    height: 35px;
    border: 0.5px solid #cfd2d3;
    box-sizing: border-box;
    border-radius: 8px 8px 0px 0px;
    flex: none;
    border-bottom: none;
    order: 0;
    flex-grow: 0;
    color: hsla(60, 1%, 23%, 0.603);
    margin: 0px 2px;
    &:first-child {
      margin: 0px 0px;
    }
    &--active {
      color: #000000;
      background: #ffffff;
      border-bottom: 1px solid #ffffff;
      position: relative;
      z-index: 3;
    }
  }
}

.tag {
  display: flex;
  float: left;
  align-items: center;
  margin-top: 10px;
  overflow-x: auto;
}

.icon__add-monitor {
  cursor: pointer;
  margin-left: 9px;
  margin-top: 7px;
}

.active-content__wrapper {
  display: flex;
  flex-direction: column;
}

.attribute-container {
  position: relative;
  z-index: 2;
  margin-top: -1px;
  border-top: 0.5px solid #cfd2d3;
}

.delete-tag {
  fill: #3c3c3b;
}

.monitor-info {
  display: flex;
  justify-content: center;
  padding: 16px 12px;
  width: 252px;

  height: 51px;
  border: 0.5px solid rgba(0, 161, 223, 0.5);
  box-sizing: border-box;
  border-radius: 4px;
  color: #00a1df;
}
