.user-table_wrapper {
  background: #ffffff;

  border-radius: 8px;
  border: 1px solid #cfd2d3;

  .table {
    border-spacing: 0;
    width: 100%;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    border-radius: 8px;
    color: #3c3c3b;
  }
  .header-table_wrapper {
    justify-content: space-between;
    background: #f8f7f7;
    border: 1px solid #cfd2d3;
    border-radius: 8px;
  }
  .header-table-item {
    text-align: left;
    padding: 17px 0px 17px 22px;
    text-transform: uppercase;
    color: rgba(60, 60, 59, 0.64);

    &:first-child {
      border-top-left-radius: 8px;
    }
    &:last-child {
      border-top-right-radius: 8px;
    }
  }

  .content {
    &-table {
      &_wrapper {
        border-top: 1px solid #cfd2d3;
      }
      &-row {
        border-spacing: 0;

        &:last-child {
          .content-table-item {
            border-bottom: 0;
          }
        }
      }
    }
  }
  .content-table-item {
    padding: 21px 0px 21px 22px;
    box-sizing: border-box;
    border-bottom: 1px solid #cfd2d3;
    border-top: 1px solid #cfd2d3;
    max-width: 186px;
    min-width: 69px;
    &:last-child {
      padding-right: 20px;
    }
    &:first-child {
      padding-left: 20px;
    }
  }

  .dropdown {
    position: absolute;
    z-index: 1000;
    background: #ffffff;
    border: 0.5px solid #cfd2d3;
    box-sizing: border-box;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);
    border-radius: 8px;
    padding: 12px;
    display: flex;
    flex-direction: column;
    margin-left: 106px;
    text-align: center;
    width: max-content;
    min-width: 172px;
  }
  .pagination {
    .dropdown {
      width: 72px !important;
      min-width: 72px !important;
    }
  }
  .dropdown-element {
    background-color: white;
    padding: 12px 0px;
    border-radius: 8px;
    margin-bottom: 12px;
    cursor: pointer;
    color: #3c3c3b;
    &:last-child {
      margin-bottom: 0px;
    }
    &:hover {
      background: rgba(207, 210, 211, 0.12);
    }
  }
  .current-dropdown-element {
    background-color: rgba(0, 161, 223, 1);
    color: #ffffff;
    &:hover {
      background-color: rgba(0, 161, 223, 1);
      color: #ffffff;
    }
  }

  .current-options {
    display: flex;
    flex-direction: row;
    height: 19px;
    align-items: center;
    cursor: pointer;
    width: max-content;
  }

  .btn-dropdown__icon {
    margin-left: 7px;
  }
  .btn-pagination {
    &__wrapper {
      width: 64px;
      display: flex;
      justify-content: space-between;
    }
    &__icon {
      cursor: pointer;
      stroke: #00a1df;

      &--active {
        stroke: #00a1df;
      }

      &--disactive {
        stroke: #3c3c3b !important;
      }
    }
  }

  .current-options-dropdown {
    padding: 6px 8px;
    background: rgba(0, 161, 223, 0.12);
    border-radius: 4px;
    color: #00a1df;
  }

  .header-table-text {
    display: flex;
    align-items: center;
  }
  .table-button-unblock {
    border-radius: 8px;
    margin-right: 21px;
    max-width: 160px;
    background: rgba(0, 161, 223, 0.12) !important;
    color: #00a1df;
  }
  .table-button-block {
    border-radius: 8px;

    background: rgba(228, 0, 58, 0.12);
    color: #e4003a;
  }

  .table-sort {
    display: flex;
    padding-top: 6px;
    margin-left: 5px;
    max-width: 24px;
    .icon--down {
      padding-top: 5px;
    }
    .icon--up {
      padding-top: -5px;
    }
  }
  .pagination {
    display: flex;
    justify-content: flex-end;
    padding: 24px;
    border-top: 1px solid #cfd2d3;
    &__info {
      margin-right: 54px;
      margin-top: 3px;
    }
    &__page {
      margin-right: 69px;
      cursor: pointer;
    }
  }
  .btn-dropdown__icon {
    padding-top: 1px;
  }
  .user-info {
    &__tooltip {
      position: absolute;
      z-index: 1000;
      padding: 10px;
      background: #ffffff;
      border: 0.5px solid #cfd2d3;
      box-sizing: border-box;
      color: hsla(60, 1%, 23%, 0.6);
      box-shadow: 0px 10px 18px rgba(0, 0, 0, 0.08);
      border-radius: 8px;
      margin-top: 2px;
      margin-left: 20px;
    }
  }
}
@media (max-width: 1182px) {
  .content-table-item {
    padding: 21px 0px 21px 10px;
    &:last-child {
      padding-right: 10px;
    }
  }
}

@media (max-width: 1124px) {
  .user-table_wrapper {
    margin: 32px 18px;
  }
  .content-table-item {
    &:last-child {
      padding-right: 5px;
    }
  }
}
@media (max-width: 921px) {
  .content-table-item {
    &:last-child {
      padding-left: 35px;
    }
  }
}
@media (max-width: 768px) {
  .user-table_wrapper {
    min-width: 670px;
  }
}
