.report {
  .report__group {
    width: 317px;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 0 20px 0;
    border: 0.5px solid #CFD2D3;
    border-radius: 8px;
    cursor: pointer;
    //tab-index: 1;

    &:hover {
      border: 0.5px solid #00A1DF;
      box-shadow: 0 0 0 2px rgba(0, 161, 223, 0.18);

      .report__icon {
        background: #00A1DF;

        --download-upper: #FFFFFF;
        --download-lower: #FFFFFF;
      }
    }

    &:focus {
      border: 0.5px solid #00A1DF;

      .report__icon {
        background: #00A1DF;

        --download-upper: #FFFFFF;
        --download-lower: #FFFFFF;
      }
    }

    transition-property: all;
    transition-duration: 0.25s;
    transition-timing-function: linear;
  }

  .report__icon {
    background: rgba(0, 161, 223, 0.12);
    border-radius: 8px;

    --download-upper: #00A1DF;
    --download-lower: #00A1DF;
  }

  .report__date-group {
    display: flex;
    align-items: center;
  }

  .report__date-year {
    margin-right: 4px;

    color: #3C3C3B;
    opacity: 0.48;

    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
  }

  .report__date-month {
    margin-left: 4px;
    text-transform: capitalize;
  }

}
