.detailed-schedule {
  position: relative;
  display: flex;
  background-color: #fff;

  &__time {
    border-right-width: 1px;
    border-right-style: solid;
    border-right-color: rgba(207, 210, 211, 0.5);
  }

  &__hour {
    font-weight: 500;
    padding: 0px 19px;
    line-height: 2.438;

    &::after {
      content: "";
      display: block;
      border-bottom-width: 1px;
      border-bottom-style: solid;
      border-bottom-color: rgba(207, 210, 211, 0.5);
      position: absolute;
      left: 0;
      width: 100%;
    }
  }

  &__bookings {
    position: relative;
    flex-grow: 1;
    margin-left: 26px;
    margin-right: 26px;
  }

  &__user {
    display: flex;
    align-items: center;
    background-color: rgba(0, 161, 223, 0.12);
    border-radius: 16px;
    padding: 4px 4px 3px;
    margin-bottom: 1px;

    &::before {
      content: '';
      display: block;
      width: 79px;
      height: 100%;
      background-color: #96e0fa3b;
      position: absolute;
      left: -106px;
      top: 0;
    }

    &-icon {
      width: 32px;
      height: 32px;
      object-fit: cover;
      border-radius: 50%;
      margin-right: 15px;
    }

    &-name {
      font-size: 14px;
      line-height: 16px;
    }
  }
}