.modal-form__background {
  position: fixed;
  overflow: auto;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 99;
}

.modal-form {
  position: absolute;
  transform: translate(-50%, -50%);
  z-index: 999;
  left: 50%;
  top: 50%;
  color: black;
  &--delete {
    .modal-form__title {
      padding-left: 0px !important;
      padding-top: 18px;
    }
  }
}

.modal-form__container {
  background: #ffffff;
  border: 0.5px solid #cfd2d3;
  box-sizing: border-box;
  backdrop-filter: blur(96px);
  border-radius: 8px;
}

.modal-form__title {
  padding-top: 40px;
  position: absolute;
  font-size: 21px;
  display: flex;
  align-items: center;

  .form__item {
    margin-bottom: 0px;
  }
}

.form_place_input {
  margin-bottom: 15px;
  margin-top: -34px;
  margin-left: 175px;
}

.place_input {
  font-family: Montserrat;
  font-style: normal;
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  border: 1px solid #cfd2d3;
  border-radius: 8px;
  width: 119px;
  padding-left: 30px;
}

.place_input-error {
  font-family: Montserrat;
  font-style: normal;
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  border: 1px solid #cfd2d3;
  border-radius: 8px;
  width: 119px;
  padding-left: 30px;
  border: 1px solid #ff0800;
  border-radius: 8px;
  width: 119px;
  height: 30px;
  padding: 5px 31px;

  .place_input:hover {
    outline: 2.2px solid rgba(0, 161, 223, 0.12);
    border: 1px solid #cfd2d3;
  }

  .place_input:active {
    outline: 2.2px solid rgba(0, 161, 223, 0.12);
    border: 1px solid rgba(0, 161, 223);
  }

  .place_input:focus {
    outline: 2.2px solid rgba(0, 161, 223, 0.12);
    border: 1px solid rgba(0, 161, 223);
  }

  .place_input-error:hover {
    outline: 2.2px solid rgba(228, 0, 58, 0.12);
    border: 1px solid #cfd2d3;
  }

  .place_input-error:active {
    outline: 2.2px solid rgba(228, 0, 58, 0.12);
    border: 1px solid rgb(97, 27, 27);
  }

  .place_input-error:focus {
    outline: 2.2px solid rgba(228, 0, 58, 0.12);
    border: 1px solid rgb(97, 27, 27);
  }
}

@media (max-width: 575px) {
  .modal-form__container {
    width: calc(100vw - 20px);
  }
  .modal-form_wrapper {
    display: flex;
    flex-direction: column;
  }
}

@media (max-width: 414px) {
  .modal-form {
  }
}
