.workspace__group {
  display: grid !important;
  grid-template-columns: repeat(auto-fit, minmax(275px, 1fr));
  grid-gap: 20px;
}

@media (max-width: 992px) {
  .workspace__group {
    padding-left: 28px;
    padding-right: 28px;
  }
}

@media (max-width: 767px) {
  .workspace__group {
    padding-left: 0px;
    padding-right: 0px;
  }
}
@media (max-width: 1347px) {
  .main__container {
    padding-left: 20px !important;
    padding-right: 20px !important;
  }

}