.block-card {
  .modal {
    &__wrapper {
      padding-top: 64px;
    }
    &-form__title {
      padding-top: 18px !important;
    }
  }
}
.block-modal {
  width: 382px;
  margin-top: 64px;
  .modal {
    &-form__title {
      padding-top: 19px;
      padding-left: 0px;
    }
  }
  .control__buttons {
    width: 382px;
  }
  &__wrapper {
  }
  .box {
    margin-top: 48px;
    .box__content {
      display: flex;
      width: 382px;
      justify-content: space-between;
      margin-bottom: 20px;
    }
  }
  .filter-box {
    &__filter {
      height: 48px;
    }
    &__period {
      width: 160px;
      &-icon {
        margin-top: 37px;
      }
    }
  }
  .react-datepicker {
    height: 355px;
    border: none;
  }
  .react-datepicker__day-names {
    margin-bottom: -3px;
  }
  .period-date {
    position: absolute;
    z-index: 20;
    margin-top: 78px;
    background-color: white;
    border: 0.5px solid #cfd2d3;
    border-radius: 8px;
    display: flex;
    &__month {
      height: 355px;
    }
  }
  .react-datepicker-year-header {
    padding-bottom: 54px;
  }
  .calendar__time-group {
    width: 160px;
  }
  .control__buttons {
    margin-top: 20px;
  }
  .ctrl-button {
    width: 181px;
  }
  .card__button {
    &--inactive {
      background: rgba(228, 0, 58, 0.12);
      color: #e4003a;
      &:hover {
        background: rgba(228, 0, 58, 0.42);
      }
    }
    &--active {
      background: rgba(0, 161, 223, 0.12);
      color: #00a1df;
      &:hover {
        background: rgba(0, 161, 223, 0.42);
      }
    }
  }
}
.modal-form__background {
  overflow-x: hidden;
}
.react-datepicker__month-text {
  margin: 11px !important;
}
.box__content {
  .time .react-datepicker {
    padding: 22px !important;
  }
  .react-datepicker-time__header {
    padding-left: 17px;
  }
  .time {
    .react-datepicker {
      width: 128px !important;
      height: 287px !important;
      padding: 26px 24px 12px;
      border: 0.5px solid #cfd2d3;
      border-radius: 8px;
      left: 80px;
      box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);
      &__header {
        text-align: start;
      }
    }
  }
}

.react-datepicker__day--in-selecting-range:not(.react-datepicker__day--in-range, .react-datepicker__month-text--in-range, .react-datepicker__quarter-text--in-range, .react-datepicker__year-text--in-range) {
  background-color: hsla(197, 100%, 44%, 0.06) !important;
}

.react-datepicker__day--in-selecting-range.react-datepicker__day--selecting-range-start {
  color: #00a1df !important;
}

.react-datepicker {
  display: none;
}
.react-datepicker__time-container .react-datepicker__time .react-datepicker__time-box ul.react-datepicker__time-list {
  scrollbar-width: none;
  -ms-overflow-style: none;
  &::-webkit-scrollbar {
    width: 0px;
    background: transparent;
  }
}

.react-datepicker__time-container
  .react-datepicker__time
  .react-datepicker__time-box
  ul.react-datepicker__time-list
  li.react-datepicker__time-list-item {
  padding: 10px !important;
}

.react-datepicker__time-container .react-datepicker__time {
  margin-top: 15px;
}

.react-datepicker__header {
  border-bottom: none !important;
}

.react-datepicker__year-dropdown {
  width: 75px !important;
  border: 0.5px solid #cfd2d3;
  box-sizing: border-box;
  box-shadow: 0px 4px 20px rgb(0 0 0 / 8%);
  border-radius: 8px;
  height: 150px;
  overflow: auto;
}
