.toast {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  display: flex;
  align-items: center;

  &__header {
    display: flex;
  }

  &__spacer {
    width: 297px;
    margin-top: 21px;
    border: 0.5px solid rgba(228, 0, 58, 0.12);
    margin-left: 25px;
  }
  &__wrapper {
    .ctrl-button {
      background: rgba(228, 0, 58, 0.12);
      backdrop-filter: blur(96px);
      border-radius: 4px;
      transform: matrix(1, 0, 0, 1, 0, 0);
      margin-top: 21px;
      color: #e4003a;
      &:hover {
        background: rgba(228, 0, 58, 0.32);
      }
    }
  }
}
.header {
  background-color: none;
  &__title {
    font-size: 16px;
    line-height: 19px;
    display: flex;
    align-items: center;
    color: #3c3c3b;
    margin-left: 12px;
  }
}
.toast__header.header {
  border-bottom: none;
  position: relative;
  background: rgba(255, 255, 255, 0.12);
}
.Toastify__toast.Toastify__toast--error {
  width: 376px;
  background: rgba(255, 255, 255, 0.12);
}
.Toastify__close-button.Toastify__close-button--light {
  margin-top: 14px;
  margin-right: 14px;
  position: absolute;
  right: 0;
}

.Toastify__toast-body {
  padding: 12px;
}

.content {
  &__item {
    display: flex;
    margin-top: 13px;
    justify-content: flex-start;
    align-items: center;

    &__icon {
      width: 6px;
      height: 6px;
      background: #e4003a;
      margin-left: 2px;
      border-radius: 50%;
      &--middle {
        background: #f3a0b2;
      }
      &--bottom {
        background: #fab526;
      }
    }

    &__subtitle {
      margin-left: 19px;
    }
    &__desctiption {
      color: hsla(60, 1%, 23%, 0.596) !important;
      margin-top: 4px;
      font-size: 13px;
      line-height: 15px;
      margin-left: 27px;
      color: hsla(60, 1%, 23%, 0.646);
    }
  }
}
