.nav__item {
  margin-top: 32px;
  list-style: none;
}

.nav__menu-link {
  display: block;
  padding: 20px 20px 20px 68px;
  position: relative;
  font-size: 15px;
  line-height: 120%;
  color: rgba(61, 61, 59, 0.8);
  transition-property: background-color, color;
  transition-duration: 0.25s;
  transition-timing-function: ease;

  &:hover {
    background-color: rgba(0, 161, 222, 0.06);
    color: #00a1df;
  }
}

.nav__menu-link--active {
  color: #00a1df;
  background-color: rgba(0, 161, 223, 0.06);

  &:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 4px;
    border-radius: 4px 0 0 4px;
    background-color: #00a1df;
  }
}
