.header {
  background-color: #fff;
  border-bottom: 2px solid #f8f7f7;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__tools {
    display: none;

    &-wrapper {
      display: flex;
      align-items: center;
    }
  }

  &__burger {
    margin-right: 24px;
  }

  &__logo {
    display: none;
  }
}

.btn-round {
  border-radius: 50%;

  &__icon {
    display: block;

    --icon-background-primary: transparent;
    --icon-path-primary: #3c3c3b;
  }
}

.form-search {
  max-width: 481px;
  width: 100%;

  &__box {
    display: flex;
    align-items: center;
  }

  &__btn {
    display: none;
    margin-right: 24px;
  }

  &__wrapper {
    position: relative;
    flex-grow: 1;

    .dropdown-search__icon {
      left: 8px;
      right: 0;
    }

    .dropdown-search__icon-global {
      position: absolute;
      left: auto;
      right: 8px;
      color: transparent;
      --icon-path-primary: transparent;
    }

    .form-search__remove-icon--blue {
      color: #cfd2d3;
      --icon-path-primary: #ffffff;
    }

    .calendar__time-group {
      padding: 15px 14px 14px 48px;
    }

    .dropdown-search__input-block {
      width: 100%;
    }

    .dropdown-search__icon--focus {
      --icon-path-primary: #cfd2d3;
    }

    .dropdown-search__icon--searching {
      --icon-path-primary: #cfd2d3;
    }

    .search-modal__group {
      position: absolute;
      z-index: 3;
    }
  }

  &__input {
    padding: 14px 48px 13px;
  }

  &__icon-search,
  &__remove-text {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  &__icon-search {
    left: 16px;

    --icon-background-primary: transparent;
    --icon-path-primary: #cfd2d3;
  }

  &__remove-text {
    right: 4px;
    padding: 9px;
    background: transparent;
  }

  &__remove-icon {
    display: block;
    color: transparent;

    &--blue {
      color: #00a1df;
      --icon-path-primary: #ffffff;
    }
  }
}

.header-search {
  background-color: #f8f7f7;
}

.header-search {
  background-color: #f8f7f7;
}

.booking-btn {
  font-size: 16px;
  line-height: 108%;
  background-color: rgba(0, 166, 130, 0.06);
  border: 0.5px solid #00a783;
  border-radius: 8px;
  color: #00a783;
  white-space: nowrap;
  padding: 11px 23px 10px;
}

.booking-btn__icon {
  display: none;
}

.user-bar {
  position: relative;
  display: flex;
  align-items: center;

  &__booking {
    margin-right: 72px;
  }

  &__group {
    display: flex;
    align-items: center;
  }

  &__info {
    margin-left: 10px;
    margin-right: 10px;
  }

  &__name {
    line-height: 120%;
    margin-bottom: 2px;
    text-transform: capitalize;
  }

  &__email {
    font-size: 12px;
    line-height: 117%;
    color: #cfd2d3;
  }

  &__action {
    background-color: transparent;
    padding: 8px 0px 8px 8px;

    &-icon {
      display: block;
      --arrow-down-out: rgba(0, 161, 222, 0.05);
      --arrow-down-inner: #00a1df;
    }
  }

  &__dropdown {
    padding: 20px;
    position: absolute;
    right: 0;
    top: 105%;
    background-color: #fff;
    border: 0.5px solid #cfd2d3;
    border-radius: 8px;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.04);
    visibility: hidden;
    opacity: 0;
    transition: 0.2s opacity, 0.2s visibility;
    z-index: 5;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: flex-start;
    filter: drop-shadow(0px 10px 18px rgba(0, 0, 0, 0.08));

    &-link {
      display: flex;
      align-items: center;
      color: #3c3c3b;
      font-size: 17px;
      line-height: calc(20 / 17);
      padding: 26px 18px;
      white-space: nowrap;
    }

    &-box {
      display: flex;
      align-items: center;
      cursor: pointer;
      margin-bottom: 26px;
      white-space: nowrap;

      --icon-inner: #f8f7f7;
      --icon-outer: #cfd2d3;

      &:last-child {
        margin-top: 0;
        margin-bottom: 0;
      }

      &:hover {
        --icon-inner: rgb(220, 240, 251);
        --icon-outer: #00a1df;
      }
    }

    &-icon {
      margin-right: 10px;
    }
  }
}

.header + .form-modal__tooltip {
  .modal-form {
    width: 422px;
    top: 50%;
    left: 55%;
    transform: translate(-50%, -50%);
  }

  .modal-form__container {
    border-radius: 14px;
  }

  .form-authtorisation {
    display: flex;
    flex-direction: column;
    padding: 20px;
    border-radius: 14px;
    gap: 12px;
  }

  .modal__wrapper {
    padding: 0;

    .control_buttons {
      display: none;
    }
  }

  .control__buttons {
    display: none;
  }

  .modal-tooltip {
    display: flex;
    gap: 12px;
  }

  .modal-tooltip {
    p {
      width: 24px;
      height: 24px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(0, 167, 131, 0.12);
      color: #00a783;
      border-radius: 4.8px;
    }
  }

  .modal-tooltip {
    span {
      font-family: Montserrat;
      font-style: normal;
      font-weight: bold;
      font-size: 21px;
      line-height: 26px;

      display: flex;
      align-items: center;

      color: #3c3c3b;
    }
  }

  .modal-text {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 130%;

    display: flex;
    align-items: center;
  }
}
.modal-form__icon-closed {
  position: absolute;
  top: 20px;
  right: 20px;
}

.header + .form-modal__profile {
  .modal-form {
    top: 50%;
    left: 55%;
    transform: translate(-50%, -50%);
  }
}

.user-bar__dropdown--is-open {
  visibility: visible;
  opacity: 1;
}

.workspace-booking-btn {
  display: none;
}

@media (max-width: 991px) {
  .workspace-booking-btn {
    display: block;
  }
}

@media (max-width: 420px) {
  .user-bar__dropdown-box {
    --pin-inner: rgba(0, 161, 223, 0.12);
    --user-profile-inner: rgb(224, 244, 251);
    --user-profile-outer: #00a1df;
    --pin-out: #00a1df;
  }

  .header + .form-modal__profile {
    .modal-form {
      top: 52%;
      left: 50%;
    }
  }

  .header + .form-modal__tooltip {
    .modal-form {
      width: 280px;
      left: 50%;
    }

    .modal-form__container {
      width: 280px;
    }
  }
}
