.rooms__box:not(:last-child) {
    margin-bottom: 48px;
}

.reservation-room__title {
    margin-bottom: 32px;
    display: flex;
    align-items: center;
    grid-gap: 24px;
}

.reservation-room__select-all {
    padding: 1px 3px;
    background-color: transparent;
    color: #00A1DF;
    font-family: 'Roboto';
    font-size: 16px;
    opacity: 0;
    visibility: hidden;
    transition: 0.2s opacity, 0.2s visibility;
}

.reservation-room__select-all--active {
    visibility: visible;
    opacity: 1;
}

.reservation-cards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(278px, 1fr));
    grid-template-rows: minmax(270px, auto);
    grid-gap: 16px;
}
