.search-modal {
  .search-modal__group {
    height: 255px;
    background: #FFFFFF;
    border: 0.5px solid #CFD2D3;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);
    width: 100%;
    padding: 12px;
    grid-gap: 2px;
    display: flex;
    flex-direction: column;
    border-radius: 8px;
    overflow-y: scroll;

    .user:not(:last-child) {
      padding: 12px;
      border-bottom: none;
    }

    .user:not(:first-child) {
      padding: 12px;
    }

    &::-webkit-scrollbar {
      width: 5px;
    }

    &::-webkit-scrollbar-thumb {
      box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    }

    .user-bar__avatar {
      width: 32px;
      height: 32px;
      line-height: 32px;
    }
  }
}

.reservation {
  .search-modal__group {
    max-height: 200px;
  }

  .line + .dropdown-search {
    .calendar__time-group {
      color: #00A1DF;
    }
  }
}

.header {
  .search-modal__group {
    max-height: 255px;
  }
}

