.reservation-card {
    background-color: #fff;
    border-width: 1px;
    border-style: solid;
    border-color: #CFD2D3;
    border-radius: 8px;
    padding: 4px;
    position: relative;

    &__preview {
        height: 109px;
        background-color: #F8F7F7;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        overflow: hidden;
    }

    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &-checkbox__inner {
        position: absolute;
        top: 6px;
        right: 6px;
    }

    &__content {
        padding: 18px 14px 7px;
    }

    &__info {
        justify-content: space-between;
    }

    &__reservations {
        justify-content: end;
        margin-top: 16px;
    }
}

.round-checkbox__field {
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
    overflow: hidden;
}

.reservation-checkbox {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    background-color: #fff;
    border-width: 1px;
    border-style: solid;
    border-color: #00A1DF;
    border-radius: 50%;
    cursor: pointer;

    &--active {
        background-color: #00A1DF;
    }
}

.info {
    display: flex;
    align-items: center;

    &__location {
        font-weight: 500;
    }

    &__room {
        font-size: 12px;
        color: rgba(61, 61, 59, 0.48);
        margin-right: 4px;
    }

    &__place {
        font-size: 18px;
    }

    &__date {
        border-width: 1px;
        border-style: solid;
        border-color: #CFD2D3;
        border-radius: 8px;
        padding: 4px 8px;
        font-size: 14px;
        line-height: 115%;
    }

    &__time {
        background-color: rgba(0, 161, 223, 0.12);
        padding: 4px 8px;
        border-radius: 8px;
        font-size: 14px;
        line-height: 115%;
        color: #00A1DF;
    }
}

.reservation-cards .card-tool__wrapper {
    border-top-width: 1px;
    border-top-style: solid;
    border-top-color: #cfd2d3;
    margin-top: 16px;
}
