.schedule-year {
  background-color: #f8f7f7;
  padding: 22px 20px 42px;

  &__wrapper {
    max-width: 1243px;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    grid-gap: 34px 10px;
  }
}

.schedule-year__name {
  text-transform: capitalize;
  font-weight: 500;
  font-size: 15px;
  margin-bottom: 6px;
  text-align: center;
}

.schedule-year__date {
  color: #cfd2d3;
  margin-left: 5px;
}

.month {
  display: grid;
  grid-template: repeat(7, 36px) / repeat(7, 36px);
  grid-gap: 2px;

  &__day,
  &__day-week {
    background-color: #fff;
    text-transform: capitalize;
    font-size: 14px;
  }

  &__day {
    &-wrapper {
      height: 100%;
      border-radius: 8px;

      &--active:hover {
        background-color: #00a1df;
        color: #fff;
      }
    }
  }
}

.month__day-week,
.month__day-wrapper {
  display: grid;
  place-content: center;
  &--inact {
    color: #fab526;
  }
  &--error {
    background: rgba(228, 0, 58, 0.12);
    color: #e4003a;
    border-radius: 0px;
    &:hover {
      background: rgba(228, 0, 58, 0.12);
      color: #e4003a;
      border: 1px solid #e4003a;
      border-radius: 8px;
    }
  }

  &--success {
    background: rgba(0, 161, 223, 0.12);
    color: #00a1df;
    border-radius: 0px;
    &:hover {
      background: rgba(0, 161, 223, 0.12);
      color: #00a1df;
      border: 1px solid #00a1df;
      border-radius: 8px;
    }
  }
  &--inactive {
    background: rgba(250, 181, 38, 0.12);
    color: #fab526;
    &:hover {
      background: rgba(250, 181, 38, 0.12);
      color: #fab526;
      border: 1px solid #fab526;
      border-radius: 8px;
    }
  }
}

.month__day-wrapper--first.month__day-wrapper--error {
  background: rgba(228, 0, 58, 0.42);
  border-top-left-radius: 8px;
}

.month__day-wrapper--first.month__day-wrapper--success {
  background: rgba(0, 161, 223, 0.42);
  border-top-left-radius: 8px;
}

.month__day-wrapper--first.month__day-wrapper--inactive {
  background: rgba(250, 181, 38, 0.42);
  border-top-left-radius: 8px;
}

.month__day-wrapper--last.month__day-wrapper--error {
  background: rgba(228, 0, 58, 0.42);
  border-bottom-right-radius: 8px;
}

.month__day-wrapper--last.month__day-wrapper--success {
  border-bottom-right-radius: 8px;
  background: rgba(0, 161, 223, 0.42);
}

.month__day-wrapper--last.month__day-wrapper--inactive {
  border-bottom-right-radius: 8px;
  background: rgba(250, 181, 38, 0.42);
}
